// ===== 3. config/AdminPortal.js (Main Portal) =====
import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Tabs, Tab, Button, Table, Badge, Alert, Modal, Form } from 'react-bootstrap';
import { getClinicConfig, getClinicData, updateClinicConfig, updateClinicData, addAppointment, resetAllData } from './dataStore';
import AdminSettings from './adminSettings';

const AdminPortal = () => {
  const [config, setConfig] = useState(() => getClinicConfig());
  const [data, setData] = useState(() => getClinicData());
  const [showAddAppointment, setShowAddAppointment] = useState(false);
  const [newAppointment, setNewAppointment] = useState({
    patientId: '', patientName: '', patientEmail: '', date: '', time: '', type: '', chiefComplaint: ''
  });

  // Listen for external updates
  useEffect(() => {
    const handleUpdate = () => {
      setConfig(getClinicConfig());
      setData(getClinicData());
    };
    window.addEventListener('clinic-data-updated', handleUpdate);
    return () => window.removeEventListener('clinic-data-updated', handleUpdate);
  }, []);

  const handleConfigChange = (newConfig) => {
    setConfig(newConfig);
    updateClinicConfig(newConfig);
  };

  const handleAddAppointment = () => {
    if (!newAppointment.patientName || !newAppointment.date || !newAppointment.time) {
      alert('Please fill required fields');
      return;
    }

    addAppointment(newAppointment, 'admin');
    setNewAppointment({ patientId: '', patientName: '', patientEmail: '', date: '', time: '', type: '', chiefComplaint: '' });
    setShowAddAppointment(false);
    alert('Appointment added successfully!');
  };

  const deletePatient = (id) => {
    if (window.confirm('Delete this patient?')) {
      const updatedPatients = data.patients.filter(p => p.id !== id);
      updateClinicData('patients', updatedPatients);
    }
  };

  return (
    <Container fluid className="py-4">
      <Row className="mb-3">
        <Col>
          <h2>Admin Portal</h2>
          <p className="text-muted">Manage system settings and mock data</p>
        </Col>
        <Col className="text-end">
          <Button variant="outline-danger" onClick={resetAllData}>Reset All</Button>
        </Col>
      </Row>

      {config.broadcastBanner && (
        <Alert variant="info" className="mb-3 text-center">
          <strong>Active Banner:</strong> {config.broadcastBanner}
        </Alert>
      )}

      <Card>
        <Card.Body>
          <Tabs defaultActiveKey="settings">
            <Tab eventKey="settings" title="Settings">
              <AdminSettings config={config} onConfigChange={handleConfigChange} />
            </Tab>

            <Tab eventKey="data" title="Mock Data">
              <Row className="g-4">
                <Col md={6}>
                  <div className="d-flex justify-content-between align-items-center mb-3">
                    <h5>Patients ({data.patients.length})</h5>
                  </div>
                  <Table responsive hover size="sm">
                    <thead>
                      <tr><th>Name</th><th>Room</th><th>Status</th><th>Actions</th></tr>
                    </thead>
                    <tbody>
                      {data.patients.map(p => (
                        <tr key={p.id}>
                          <td><strong>{p.name}</strong><br/><small>{p.id}</small></td>
                          <td>{p.room}</td>
                          <td>
                            <Badge bg={p.status === 'stable' ? 'success' : p.status === 'critical' ? 'danger' : 'warning'}>
                              {p.status}
                            </Badge>
                          </td>
                          <td>
                            <Button size="sm" variant="outline-danger" onClick={() => deletePatient(p.id)}>Delete</Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>

                <Col md={6}>
                  <div className="d-flex justify-content-between align-items-center mb-3">
                    <h5>Appointments ({data.appointments.length})</h5>
                    <Button variant="outline-primary" onClick={() => setShowAddAppointment(true)}>+ Add</Button>
                  </div>
                  <Table responsive hover size="sm">
                    <thead>
                      <tr><th>Patient</th><th>Date/Time</th><th>Type</th><th>Added By</th><th>Status</th></tr>
                    </thead>
                    <tbody>
                      {data.appointments.map(a => (
                        <tr key={a.id}>
                          <td>{a.patientName}</td>
                          <td>{a.date}<br/><small>{a.time}</small></td>
                          <td>{a.type}</td>
                          <td>
                            <Badge bg={a.addedBy === 'patient' ? 'info' : 'secondary'}>
                              {a.addedBy}
                            </Badge>
                          </td>
                          <td>
                            <Badge bg={a.status === 'completed' ? 'success' : a.status === 'in-progress' ? 'warning' : 'primary'}>
                              {a.status}
                            </Badge>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>

              <Row className="mt-4">
                <Col md={6}>
                  <h5>Medications ({data.medications.length})</h5>
                  <Table responsive hover size="sm">
                    <thead>
                      <tr><th>Patient</th><th>Medication</th><th>Status</th></tr>
                    </thead>
                    <tbody>
                      {data.medications.map(m => (
                        <tr key={m.id}>
                          <td>{m.patientName}</td>
                          <td>{m.medication}</td>
                          <td>
                            <Badge bg={m.status === 'administered' ? 'success' : m.status === 'overdue' ? 'danger' : 'warning'}>
                              {m.status}
                            </Badge>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </Tab>
          </Tabs>
        </Card.Body>
      </Card>

      {/* Add Appointment Modal */}
      <Modal show={showAddAppointment} onHide={() => setShowAddAppointment(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Add Appointment (Admin)</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Row>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Patient Name *</Form.Label>
                  <Form.Control
                    value={newAppointment.patientName}
                    onChange={e => setNewAppointment({...newAppointment, patientName: e.target.value})}
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    value={newAppointment.patientEmail}
                    onChange={e => setNewAppointment({...newAppointment, patientEmail: e.target.value})}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={4}>
                <Form.Group className="mb-3">
                  <Form.Label>Date *</Form.Label>
                  <Form.Control
                    type="date"
                    value={newAppointment.date}
                    onChange={e => setNewAppointment({...newAppointment, date: e.target.value})}
                  />
                </Form.Group>
              </Col>
              <Col md={4}>
                <Form.Group className="mb-3">
                  <Form.Label>Time *</Form.Label>
                  <Form.Control
                    type="time"
                    value={newAppointment.time}
                    onChange={e => setNewAppointment({...newAppointment, time: e.target.value})}
                  />
                </Form.Group>
              </Col>
              <Col md={4}>
                <Form.Group className="mb-3">
                  <Form.Label>Type</Form.Label>
                  <Form.Select
                    value={newAppointment.type}
                    onChange={e => setNewAppointment({...newAppointment, type: e.target.value})}
                  >
                    <option value="">Select...</option>
                    {config.appointmentTypes.map(type => (
                      <option key={type} value={type}>{type}</option>
                    ))}
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            <Form.Group className="mb-3">
              <Form.Label>Chief Complaint</Form.Label>
              <Form.Control
                as="textarea"
                rows={2}
                value={newAppointment.chiefComplaint}
                onChange={e => setNewAppointment({...newAppointment, chiefComplaint: e.target.value})}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowAddAppointment(false)}>Cancel</Button>
          <Button variant="primary" onClick={handleAddAppointment}>Add Appointment</Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default AdminPortal;
