// ===== 2. config/AdminSettings.js (Settings Management) =====
import React, { useState } from 'react';
import { Row, Col, Form, Button, Table, InputGroup, Card } from 'react-bootstrap';

const AdminSettings = ({ config, onConfigChange }) => {
  const [localConfig, setLocalConfig] = useState(config);
  const [newAppointmentType, setNewAppointmentType] = useState('');

  const saveSettings = () => {
    onConfigChange(localConfig);
    alert('Settings saved successfully!');
  };

  const addAppointmentType = () => {
    if (newAppointmentType.trim() && !localConfig.appointmentTypes.includes(newAppointmentType.trim())) {
      setLocalConfig({
        ...localConfig,
        appointmentTypes: [...localConfig.appointmentTypes, newAppointmentType.trim()]
      });
      setNewAppointmentType('');
    }
  };

  const removeAppointmentType = (index) => {
    setLocalConfig({
      ...localConfig,
      appointmentTypes: localConfig.appointmentTypes.filter((_, i) => i !== index)
    });
  };

  const setBroadcast = (message) => {
    const newConfig = { ...localConfig, broadcastBanner: message };
    setLocalConfig(newConfig);
    onConfigChange(newConfig);
  };

  return (
    <div className="py-4">
      <Row className="g-4">
        <Col md={6}>
          {/* Vitals Thresholds Section */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">Vitals Thresholds</h5>
            </Card.Header>
            <Card.Body>
              <Form.Group className="mb-3">
                <Form.Label className="fw-semibold">Vitals Overdue (hours)</Form.Label>
                <Form.Control
                  type="number"
                  value={localConfig.vitalsThresholds.overdueHours}
                  onChange={e => setLocalConfig({
                    ...localConfig,
                    vitalsThresholds: { ...localConfig.vitalsThresholds, overdueHours: Number(e.target.value) }
                  })}
                  style={{ maxWidth: '120px' }}
                />
              </Form.Group>
              
              <Row className="g-3">
                <Col>
                  <Form.Group>
                    <Form.Label className="fw-semibold">Fever (F)</Form.Label>
                    <Form.Control
                      type="number"
                      step="0.1"
                      value={localConfig.vitalsThresholds.feverF}
                      onChange={e => setLocalConfig({
                        ...localConfig,
                        vitalsThresholds: { ...localConfig.vitalsThresholds, feverF: Number(e.target.value) }
                      })}
                    />
                  </Form.Group>
                </Col>
                <Col>
                  <Form.Group>
                    <Form.Label className="fw-semibold">Tachycardia (bpm)</Form.Label>
                    <Form.Control
                      type="number"
                      value={localConfig.vitalsThresholds.tachyBpm}
                      onChange={e => setLocalConfig({
                        ...localConfig,
                        vitalsThresholds: { ...localConfig.vitalsThresholds, tachyBpm: Number(e.target.value) }
                      })}
                    />
                  </Form.Group>
                </Col>
                <Col>
                  <Form.Group>
                    <Form.Label className="fw-semibold">Hypoxia (%)</Form.Label>
                    <Form.Control
                      type="number"
                      value={localConfig.vitalsThresholds.hypoxiaPct}
                      onChange={e => setLocalConfig({
                        ...localConfig,
                        vitalsThresholds: { ...localConfig.vitalsThresholds, hypoxiaPct: Number(e.target.value) }
                      })}
                    />
                  </Form.Group>
                </Col>
              </Row>
            </Card.Body>
          </Card>

          {/* Feature Flags Section */}
          <Card>
            <Card.Header>
              <h5 className="mb-0">Feature Flags</h5>
            </Card.Header>
            <Card.Body>
              <Row className="g-2">
                {Object.entries(localConfig.featureFlags).map(([key, val]) => (
                  <Col md={6} key={key}>
                    <div className="p-2 bg-light rounded">
                      <Form.Check
                        type="switch"
                        label={key}
                        checked={val}
                        onChange={e => setLocalConfig({
                          ...localConfig,
                          featureFlags: { ...localConfig.featureFlags, [key]: e.target.checked }
                        })}
                        className="fw-semibold"
                      />
                    </div>
                  </Col>
                ))}
              </Row>
            </Card.Body>
          </Card>
        </Col>

        <Col md={6}>
          {/* Appointment Types Section */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">Appointment Types</h5>
            </Card.Header>
            <Card.Body>
              <Table size="sm" bordered className="mb-3">
                <tbody>
                  {localConfig.appointmentTypes.map((type, i) => (
                    <tr key={i}>
                      <td>{type}</td>
                      <td className="text-end" style={{ width: '100px' }}>
                        <Button size="sm" variant="outline-danger" onClick={() => removeAppointmentType(i)}>
                          Remove
                        </Button>
                      </td>
                    </tr>
                  ))}
                  <tr>
                    <td colSpan={2}>
                      <InputGroup>
                        <Form.Control
                          placeholder="Add new appointment type..."
                          value={newAppointmentType}
                          onChange={e => setNewAppointmentType(e.target.value)}
                          onKeyPress={e => e.key === 'Enter' && addAppointmentType()}
                        />
                        <Button variant="outline-primary" onClick={addAppointmentType}>Add</Button>
                      </InputGroup>
                    </td>
                  </tr>
                </tbody>
              </Table>
            </Card.Body>
          </Card>

          {/* Broadcast Banner Section */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">Broadcast Banner</h5>
            </Card.Header>
            <Card.Body>
              <Form.Control
                as="textarea"
                rows={3}
                placeholder="System-wide message (leave empty to hide)..."
                value={localConfig.broadcastBanner}
                onChange={e => setLocalConfig({ ...localConfig, broadcastBanner: e.target.value })}
                className="mb-3"
              />
              
              <div className="d-flex gap-2">
                <Button variant="outline-secondary" onClick={() => setBroadcast('')}>
                  Clear Banner
                </Button>
                <Button variant="primary" onClick={saveSettings}>
                  Save Settings
                </Button>
              </div>
            </Card.Body>
          </Card>

          {/* Role Permissions Section */}
          <Card>
            <Card.Header>
              <h5 className="mb-0">Role Permissions</h5>
            </Card.Header>
            <Card.Body>
              {Object.entries(localConfig.roles).map(([role, caps]) => (
                <div key={role} className="mb-3 p-3 bg-light rounded">
                  <h6 className="text-capitalize mb-2 fw-bold">{role}</h6>
                  <Row className="g-2">
                    {Object.entries(caps).map(([cap, val]) => (
                      <Col md={6} key={cap}>
                        <Form.Check
                          type="switch"
                          label={cap}
                          checked={val}
                          onChange={e => setLocalConfig({
                            ...localConfig,
                            roles: { ...localConfig.roles, [role]: { ...caps, [cap]: e.target.checked } }
                          })}
                          className="small"
                        />
                      </Col>
                    ))}
                  </Row>
                </div>
              ))}
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default AdminSettings;