// Storage utilities
const loadFromStorage = (key, defaultValue) => {
  try {
    const stored = localStorage.getItem(key);
    return stored ? JSON.parse(stored) : defaultValue;
  } catch (error) {
    console.warn(`Failed to load ${key}:`, error);
    return defaultValue;
  }
};

const saveToStorage = (key, data) => {
  try {
    localStorage.setItem(key, JSON.stringify(data));
  } catch (error) {
    console.warn(`Failed to save ${key}:`, error);
  }
};

// Public API
export const getClinicConfig = () => loadFromStorage(CONFIG_KEY, defaultConfig);
export const getClinicData = () => loadFromStorage(DATA_KEY, defaultData);

export const updateClinicConfig = (newConfig) => {
  saveToStorage(CONFIG_KEY, newConfig);
  window.dispatchEvent(new CustomEvent('clinic-data-updated', { detail: { config: newConfig } }));
};

export const updateClinicData = (key, newData) => {
  const currentData = getClinicData();
  const updatedData = { ...currentData, [key]: newData };
  saveToStorage(DATA_KEY, updatedData);
  window.dispatchEvent(new CustomEvent('clinic-data-updated', { detail: { key, data: newData } }));
};

export const addAppointment = (appointment, addedBy = 'admin') => {
  const data = getClinicData();
  const newAppointment = {
    ...appointment,
    id: Math.max(0, ...data.appointments.map(a => a.id)) + 1,
    addedBy,
    addedAt: new Date().toISOString(),
    status: appointment.status || 'scheduled'
  };
  
  const updatedAppointments = [...data.appointments, newAppointment];
  updateClinicData('appointments', updatedAppointments);
  return newAppointment;
};

export const resetAllData = () => {
  saveToStorage(CONFIG_KEY, defaultConfig);
  saveToStorage(DATA_KEY, defaultData);
  window.dispatchEvent(new CustomEvent('clinic-data-updated', { detail: { reset: true } }));
};
