import React from 'react';
import { Modal, Button, Table, Badge } from 'react-bootstrap';

const Pill = ({ children, variant = 'secondary' }) => (
  <Badge bg={variant} className="text-uppercase" style={{ fontSize: '0.7rem' }}>
    {children}
  </Badge>
);

/**
 * Reusable lab result modal.
 * Props:
 *  - show: boolean
 *  - onHide: fn
 *  - lab: {
 *      patientName, test, date, status, critical, summary,
 *      orderedBy, components?: [{ name, value, unit, range }]
 *    }
 */
const LabResultModal = ({ show, onHide, lab }) => {
  if (!lab) return null;

  return (
    <Modal show={show} onHide={onHide} size="lg" aria-labelledby="lab-result-modal-title">
      <Modal.Header closeButton>
        <Modal.Title id="lab-result-modal-title">
          Lab Result  {lab.patientName} &nbsp;
          <small className="text-muted">({lab.test})</small>
        </Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <div className="d-flex flex-wrap gap-2 mb-3">
          <Pill variant={lab.critical ? 'danger' : 'secondary'}>
            {lab.critical ? 'CRITICAL' : 'NON-CRITICAL'}
          </Pill>
          <Pill variant={lab.status === 'pending_review' ? 'warning' : 'success'}>
            {lab.status.replace('_', ' ')}
          </Pill>
          <Pill variant="info">{lab.date}</Pill>
          {lab.orderedBy && <Pill variant="dark">Ordered by {lab.orderedBy}</Pill>}
        </div>

        {lab.summary && <p className="mb-3"><strong>Summary:</strong> {lab.summary}</p>}

        {Array.isArray(lab.components) && lab.components.length > 0 && (
          <Table bordered hover responsive>
            <thead>
              <tr>
                <th>Analyte</th>
                <th>Result</th>
                <th>Reference Range</th>
              </tr>
            </thead>
            <tbody>
              {lab.components.map((c, i) => (
                <tr key={i}>
                  <td>{c.name}</td>
                  <td>{c.value ?? ''} {c.unit || ''}</td>
                  <td>{c.range || ''}</td>
                </tr>
              ))}
            </tbody>
          </Table>
        )}
      </Modal.Body>

      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}>Close</Button>
        {/* Hook up your mark reviewed action here if needed */}
        {lab.status === 'pending_review' && (
          <Button
            variant="primary"
            onClick={onHide}
            title="Mark as reviewed (stub)"
          >
            Mark Reviewed
          </Button>
        )}
      </Modal.Footer>
    </Modal>
  );
};

export default LabResultModal;
