// src/utils/appointmentUtils.js
import { addAppointment } from '../config/dataStore';

export const createAppointment = (appointmentData, addedBy = 'patient') => {
  // Standardize the appointment data structure
  const standardizedAppointment = {
    patientId: appointmentData.patientId,
    patientName: appointmentData.patientName,
    patientEmail: appointmentData.patientEmail,
    patientDob: appointmentData.patientDob,
    patientPhone: appointmentData.patientPhone,
    date: appointmentData.date || appointmentData.preferredDate,
    time: appointmentData.time || appointmentData.preferredTime,
    type: appointmentData.type,
    chiefComplaint: appointmentData.chiefComplaint || appointmentData.reason,
    complaintDate: appointmentData.complaintDate || new Date().toISOString().split('T')[0],
    status: appointmentData.status || 'scheduled',
    priority: appointmentData.priority || 'normal'
  };

  return addAppointment(standardizedAppointment, addedBy);
};

// Helper function to validate appointment data
export const validateAppointmentData = (appointmentData) => {
  const errors = [];
  
  if (!appointmentData.patientName) {
    errors.push('Patient name is required');
  }
  
  if (!appointmentData.date && !appointmentData.preferredDate) {
    errors.push('Date is required');
  }
  
  if (!appointmentData.time && !appointmentData.preferredTime) {
    errors.push('Time is required');
  }
  
  if (!appointmentData.type) {
    errors.push('Appointment type is required');
  }
  
  return {
    isValid: errors.length === 0,
    errors
  };
};

// Helper function to format appointment data for display
export const formatAppointmentForDisplay = (appointment) => {
  return {
    ...appointment,
    displayDate: new Date(appointment.date).toLocaleDateString(),
    displayTime: appointment.time,
    displayStatus: appointment.status.charAt(0).toUpperCase() + appointment.status.slice(1),
    displayPriority: appointment.priority.charAt(0).toUpperCase() + appointment.priority.slice(1)
  };
};