// src/components/ChatModule.js
import React, { useState, useEffect, useRef } from 'react';
import { Modal, Form, Button, ListGroup, Badge, Alert, InputGroup, Spinner } from 'react-bootstrap';
import './chatModule.css';
/* ------------ config ------------ */
// In production, call the same origin the app was loaded from (cpanel proxies to Node).
// In dev, use REACT_APP_API_BASE or default to localhost:5000.
const API_BASE =
  process.env.NODE_ENV === 'production'
    ? window.location.origin
    : (process.env.REACT_APP_API_BASE || 'http://localhost:5000');

/* ------------ helpers ------------ */
const asArray = (maybe, key) => {
  if (Array.isArray(maybe)) return maybe;
  if (maybe && key && Array.isArray(maybe[key])) return maybe[key];
  return [];
};

// Increased timeout for production environment
const fetchWithTimeout = (url, options = {}, ms = 30000) => { // Changed from 8000 to 30000
  const ctrl = new AbortController();
  const t = setTimeout(() => ctrl.abort(), ms);
  return fetch(url, { ...options, signal: ctrl.signal }).finally(() => clearTimeout(t));
};

const byTimeAsc = (a, b) =>
  new Date(a.timestamp || a.createdAt || 0) - new Date(b.timestamp || b.createdAt || 0);

const safeTime = (t) => {
  const d = new Date(t || Date.now());
  const today = new Date();
  if (d.toString() === 'Invalid Date') return '';
  if (d.toDateString() === today.toDateString()) {
    return d.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' });
  }
  return d.toLocaleDateString('en-US', {
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
};

/* ------------ simulated data (fallbacks) ------------ */
const SIM_PROVIDERS = [
  { id: 'dr-smith', name: 'Dr. Emily Smith', role: 'doctor', specialty: 'Cardiology', department: 'Cardiology' },
  { id: 'dr-johnson', name: 'Dr. Michael Johnson', role: 'doctor', specialty: 'General Practice', department: 'Primary Care' }
];

const SIM_CONVERSATIONS = [
  {
    userId: 'dr-smith',
    userName: 'Dr. Emily Smith',
    lastMessage: 'Please monitor your BP this week.',
    lastMessageTime: new Date(Date.now() - 60 * 60 * 1000).toISOString(),
    unreadCount: 0,
    specialty: 'Cardiology'
  },
  {
    userId: 'dr-johnson',
    userName: 'Dr. Michael Johnson',
    lastMessage: 'Let me know if the symptoms persist.',
    lastMessageTime: new Date(Date.now() - 5 * 60 * 60 * 1000).toISOString(),
    unreadCount: 1,
    specialty: 'General Practice'
  }
];

/* ------------ component ------------ */
const ChatModule = ({ show, onHide, currentUser }) => {
  const [providers, setProviders] = useState(SIM_PROVIDERS);
  const [conversations, setConversations] = useState(SIM_CONVERSATIONS);
  const [selectedRecipient, setSelectedRecipient] = useState(null);

  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [sending, setSending] = useState(false);
  const [showProviderList, setShowProviderList] = useState(true);
  const [error, setError] = useState(null); // Added error state

  const messagesEndRef = useRef(null);
  const messagePollingRef = useRef(null);
  const inFlightRef = useRef(false); // prevent overlapping polls

  const user = currentUser || { id: 'P001234', name: 'Sarah Johnson', role: 'patient' };

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  useEffect(() => {
    if (show) {
      fetchProviders();
      fetchConversations();
    } else {
      clearInterval(messagePollingRef.current);
      messagePollingRef.current = null;
      setSelectedRecipient(null);
      setShowProviderList(true);
      setMessages([]);
      setError(null); // Clear error on close
    }
    return () => {
      clearInterval(messagePollingRef.current);
      messagePollingRef.current = null;
    };
  }, [show]);

  useEffect(() => {
    clearInterval(messagePollingRef.current);
    messagePollingRef.current = null;

    if (!selectedRecipient) return;

    fetchMessages(); // initial
    messagePollingRef.current = setInterval(fetchMessages, 5000); // Increased from 3000 to 5000

    return () => {
      clearInterval(messagePollingRef.current);
      messagePollingRef.current = null;
    };
  }, [selectedRecipient]); // eslint-disable-line react-hooks/exhaustive-deps

  /* -------- API calls -------- */
  const fetchProviders = async () => {
    try {
      console.log('Fetching providers from:', `${API_BASE}/api/messages/providers`);
      const res = await fetchWithTimeout(`${API_BASE}/api/messages/providers`, {}, 15000);
      
      if (!res.ok) {
        console.error(`Provider fetch failed with status: ${res.status}`);
        throw new Error(`HTTP ${res.status}`);
      }
      
      const data = await res.json();
      const list = asArray(data, 'providers');
      setProviders(list.length ? list : SIM_PROVIDERS);
    } catch (err) {
      console.error('Error fetching providers:', err.message);
      setProviders(SIM_PROVIDERS);
    }
  };

  const fetchConversations = async () => {
    try {
      console.log('Fetching conversations for user:', user.id);
      const res = await fetchWithTimeout(
        `${API_BASE}/api/messages/conversations/${user.id}`,
        {},
        15000
      );
      
      if (!res.ok) {
        console.error(`Conversations fetch failed with status: ${res.status}`);
        throw new Error(`HTTP ${res.status}`);
      }
      
      const data = await res.json();
      const list = asArray(data, 'conversations');
      setConversations(list.length ? list : SIM_CONVERSATIONS);
    } catch (err) {
      console.error('Error fetching conversations:', err.message);
      setConversations(SIM_CONVERSATIONS);
    }
  };

  const fetchMessages = async () => {
    if (!selectedRecipient || inFlightRef.current) return;
    inFlightRef.current = true;
    setLoading(true);
    setError(null); // Clear previous errors
    
    try {
      const url = `${API_BASE}/api/messages/messages/${user.id}/${selectedRecipient.id}`;
      console.log('Fetching messages from:', url);
      
      const res = await fetchWithTimeout(url, {}, 30000); // 30 second timeout
      
      if (!res.ok) {
        const errorText = await res.text().catch(() => `Status ${res.status}`);
        throw new Error(`HTTP ${res.status}: ${errorText}`);
      }
      
      const data = await res.json();
      const raw = asArray(data, 'messages');
      const keyOf = (m) =>
        m._id || `${m.senderId}|${m.recipientId}|${m.message}|${m.timestamp || m.createdAt}`;
      const uniq = Array.from(new Map(raw.map(m => [keyOf(m), m])).values()).sort(byTimeAsc);

      setMessages(uniq);
      console.log(`Fetched ${uniq.length} messages successfully`);
    } catch (err) {
      if (err.name === 'AbortError') {
        console.error('Request timed out after 30 seconds');
        setError('Connection timed out. Please check your internet connection.');
      } else {
        console.error('Error fetching messages:', err.message);
        setError('Unable to load messages. Please try again.');
      }
      setMessages([]); // show "No messages yet"
    } finally {
      setLoading(false);
      inFlightRef.current = false;
    }
  };

  const sendMessage = async (e) => {
    e.preventDefault();
    if (!newMessage.trim() || !selectedRecipient) return;

    setSending(true);
    setError(null);
    
    try {
      const payload = {
        senderId: user.id,
        senderName: user.name,
        senderRole: user.role,
        recipientId: selectedRecipient.id,
        recipientName: selectedRecipient.name,
        recipientRole: selectedRecipient.role,
        message: newMessage.trim(),
        messageType: 'text',
        priority: 'normal'
      };

      console.log('Sending message:', payload);
      
      const res = await fetchWithTimeout(`${API_BASE}/api/messages/send`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      }, 30000); // 30 second timeout
      
      if (!res.ok) {
        const body = await res.text().catch(() => '');
        throw new Error(`Send failed (${res.status}): ${body}`);
      }

      setNewMessage('');
      fetchMessages(); // reflect server truth

      setConversations(prev => {
        const updated = {
          userId: selectedRecipient.id,
          userName: selectedRecipient.name,
          lastMessage: payload.message,
          lastMessageTime: new Date().toISOString(),
          unreadCount: 0,
          specialty: selectedRecipient.specialty || selectedRecipient.department
        };
        const rest = prev.filter(c => c.userId !== selectedRecipient.id);
        return [updated, ...rest];
      });
      
      console.log('Message sent successfully');
    } catch (err) {
      console.error('Error sending message:', err);
      setError('Failed to send message. Please try again.');
    } finally {
      setSending(false);
    }
  };

  /* -------- UI actions -------- */
  const selectRecipient = (recipient) => {
    setSelectedRecipient(recipient);
    setShowProviderList(false);
    setMessages([]); // reset while loading
    setError(null); // clear any previous errors
  };

  const backToProviderList = () => {
    setSelectedRecipient(null);
    setShowProviderList(true);
    setMessages([]);
    setError(null);
    clearInterval(messagePollingRef.current);
    messagePollingRef.current = null;
  };

  return (
    <Modal show={show} onHide={onHide} size="xl" className="chat-modal">
      <Modal.Header closeButton>
        <Modal.Title>
          {showProviderList ? 'Message Provider' : `Chat with ${selectedRecipient?.name}`}
        </Modal.Title>
      </Modal.Header>

      <Modal.Body style={{ height: '500px',padding: 0 }}>
       {showProviderList ? (
  <div className="provider-list-container">
    <Alert variant="info">Select a healthcare provider to start a conversation</Alert>

    {conversations.length > 0 && (
      <div className="recent-conversations">
        <h6 className="provider-section-header">Recent Conversations</h6>
        <div className="chat-conversation-list">
          {conversations.map((conv) => (
            <div
              key={conv.userId}
              className="chat-conversation-item"
              onClick={() =>
                selectRecipient({
                  id: conv.userId,
                  name: conv.userName,
                  role: 'doctor',
                  specialty: conv.specialty
                })
              }
            >
              <div className="conversation-item">
                <div className="conversation-content">
                  <div className="conversation-name">
                    {conv.userName}
                    {conv.unreadCount > 0 && (
                      <Badge className="unread-badge">{conv.unreadCount}</Badge>
                    )}
                  </div>
                  <p className="conversation-preview">{conv.lastMessage}</p>
                </div>
                <div className="conversation-time">{safeTime(conv.lastMessageTime)}</div>
              </div>
            </div>
          ))}
        </div>
      </div>
    )}

    <div className="all-providers">
      <h6 className="provider-section-header">All Healthcare Providers</h6>
      <div className="chat-provider-list">
        {providers.map((provider) => (
          <div
            key={provider.id}
            className="chat-provider-item"
            onClick={() => selectRecipient(provider)}
          >
            <div className="provider-info">
              <div className="provider-details">
                <div className="provider-name">{provider.name}</div>
                <p className="provider-specialty">
                  {(provider.specialty || provider.department || 'Provider')}  {provider.role}
                </p>
              </div>
              <div className="provider-actions">
                <Badge className="message-badge">Message</Badge>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  </div>
        ) : (
          <div className="d-flex flex-column h-100">
            <div className="chat-header p-3 border-bottom bg-light">
              <Button variant="outline-secondary" size="sm" onClick={backToProviderList} className="me-3">
                 Back
              </Button>
              <strong>{selectedRecipient?.name}</strong>
              <span className="text-muted ms-2">
                {selectedRecipient?.specialty || selectedRecipient?.department}
              </span>
            </div>

            {/* Error Alert */}
            {error && (
              <Alert variant="danger" className="m-3" dismissible onClose={() => setError(null)}>
                {error}
              </Alert>
            )}

            <div className="flex-grow-1 overflow-auto p-3 chat-messages">
              {loading && messages.length === 0 ? (
                <div className="text-center"><Spinner animation="border" variant="primary" /></div>
              ) : messages.length === 0 && !error ? (
                <Alert variant="info" className="text-center">No messages yet. Start the conversation!</Alert>
              ) : (
                <>
                  {messages.map((msg, index) => (
                    <div key={index} className={`message-wrapper ${msg.senderId === user.id ? 'sent' : 'received'}`}>
                      <div className={`message-bubble ${msg.senderId === user.id ? 'sent' : 'received'}`}>
                        <div className="message-content">{msg.message}</div>
                        <div className="message-time">{safeTime(msg.timestamp || msg.createdAt)}</div>
                      </div>
                    </div>
                  ))}
                  <div ref={messagesEndRef} />
                </>
              )}
            </div>

            <div className="p-3 border-top">
              <Form onSubmit={sendMessage}>
                <InputGroup>
                  <Form.Control
                    type="text"
                    placeholder="Type your message..."
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    disabled={sending}
                  />
                  <Button variant="primary" type="submit" disabled={!newMessage.trim() || sending}>
                    {sending ? <Spinner animation="border" size="sm" /> : 'Send'}
                  </Button>
                </InputGroup>
              </Form>
              <small className="text-muted">Providers typically respond within 2448 hours for non-urgent messages</small>
            </div>
          </div>
        )}
      </Modal.Body>
    </Modal>
  );
};

export default ChatModule;