import React, { useState } from 'react';
import DoctorDashboard from './components/DoctorDashboard';
import NurseDashboard from './components/NurseDashboard';
import PatientDashboard from './components/PatientDashboard';
import GlobalBanner from './components/globalBanner';
import AdminPortal from './config/adminPortal';
import 'bootstrap/dist/css/bootstrap.min.css';
import './layout.css';
import './typography.css';                       
import './nav.css';
import './components.css';
import './overrides.css';

function App() {
  const [currentView, setCurrentView] = useState('home');

  const goHome = () => setCurrentView('home');

  // Simple render function - no context provider needed
  const renderDashboard = (DashboardComponent) => (
    <div>
      <div className="bg-light p-2">
        <button className="btn btn-outline-secondary btn-sm" onClick={goHome}>
           Back to Home
        </button>
      </div>
      <DashboardComponent />
    </div>
  );

  if (currentView === 'doctor-dashboard') {
    return renderDashboard(DoctorDashboard);
  }

  if (currentView === 'nurse-dashboard') {
    return renderDashboard(NurseDashboard);
  }

  if (currentView === 'patient-dashboard') {
    return renderDashboard(PatientDashboard);
  }

  if (currentView === 'admin-portal') {
    return renderDashboard(AdminPortal);
  }

  return (
    <div className="container mt-5 fade-in-up">
      <div className="row justify-content-center">
        <div className="col-md-8">
          <div className="card">
            <div className="card-header">
              <h1 className="text-center">Telemedicine App</h1>
            </div>
            <div className="card-body">
              <div className="alert alert-success">Backend ready for development</div>
                    <GlobalBanner />

              <div className="mt-4">
                <h3>Frontend is working!</h3>
                <p>Select a portal to continue:</p>
                
                <button
                  className="btn btn-primary btn-lg mb-2 w-100"
                  onClick={() => setCurrentView('doctor-dashboard')}
                >
                  Doctor Dashboard
                </button>
                
                <button
                  className="btn btn-success btn-lg mb-2 w-100"
                  onClick={() => setCurrentView('nurse-dashboard')}
                >
                  Nurse Dashboard
                </button>
                
                <button
                  className="btn btn-info btn-lg mb-2 w-100"
                  onClick={() => {
                    console.log('Clicking Patient Portal button');
                    setCurrentView('patient-dashboard');
                  }}
                >
                  Patient Portal
                </button>
                
                <button 
                  className="btn btn-warning btn-lg mb-2 w-100"
                  onClick={() => setCurrentView('admin-portal')}
                >
                  Admin Portal
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;