import React from 'react';
import { Table, Badge, Button } from 'react-bootstrap';

const cell = (v) => (v ?? '') || <span className="text-muted"></span>;

export default function PatientListView({
  patients = [],
  onViewRecord,     // (patient) => void
  size = 'sm',
  showActions = true,
}) {
  return (
    <Table responsive hover size={size}>
      <thead>
        <tr>
          <th>ID</th>
          <th>Patient</th>
          <th>Age/Sex</th>
          <th>Dept / Room</th>
          <th>Condition / Chief</th>
          <th>Status / Triage</th>
          {showActions && <th>Actions</th>}
        </tr>
      </thead>
      <tbody>
        {patients.map((p) => {
          const dept = p.department || p.dept;
          const triage = p.triageLevel;
          const status = p.status;
          const room = p.room;
          const condition = p.condition || '';
          const chief = p.chiefComplaint || '';

          return (
            <tr key={String(p.id)}>
              <td><small>{cell(p.id)}</small></td>
              <td>
                <strong>{cell(p.name)}</strong>
                {p.doctor && (
                  <>
                    <br /><small className="text-muted">Dr: {p.doctor}</small>
                  </>
                )}
              </td>
              <td><small>{p.age ? `${p.age}y` : ''} {p.sex || ''}</small></td>
              <td>
                {dept && <Badge bg="secondary" className="me-1">{dept.toUpperCase()}</Badge>}
                {room && <Badge bg="light" text="dark">Rm {room}</Badge>}
              </td>
              <td>
                {condition || chief ? (
                  <>
                    {condition && <div>{condition}</div>}
                    {chief && <small className="text-muted">{chief}</small>}
                  </>
                ) : ''}
              </td>
              <td>
                {typeof triage !== 'undefined' ? (
                  <Badge bg={triage <= 2 ? 'danger' : triage === 3 ? 'info' : 'secondary'}>
                    TRIAGE {triage}
                  </Badge>
                ) : status ? (
                  <Badge
                    bg={
                      status === 'critical' ? 'danger'
                        : status === 'monitoring' ? 'warning'
                        : status === 'stable' ? 'success'
                        : 'secondary'
                    }
                  >
                    {String(status).toUpperCase()}
                  </Badge>
                ) : ''}
              </td>

              {showActions && (
                <td>
                  <Button
                    size="sm"
                    variant="outline-dark"
                    onClick={() => onViewRecord?.(p)}
                  >
                    View Record
                  </Button>
                </td>
              )}
            </tr>
          );
        })}
        {patients.length === 0 && (
          <tr>
            <td colSpan={7} className="text-center text-muted py-4">
              No patients found
            </td>
          </tr>
        )}
      </tbody>
    </Table>
  );
}
