// src/components/DoctorDashboard.js
import React, { useState, useEffect } from 'react';
import {
  Container, Row, Col, Card, Button, Badge, Form,
  Modal, Alert, Accordion, Table
} from 'react-bootstrap';
import MedicalRecordModule from './MedicalRecordModule';
import { getClinicConfig, getClinicData, updateClinicData } from '../config/dataStore';
import { createAppointment } from './appointmentUtils';
import ChatModule from './ChatModule';
import LabResultModal from './LabResultModal';
import PatientAssignmentModule from './PatientAssignmentModule';

const DoctorDashboard = () => {
  // Get data from admin portal
  const [config, setConfig] = useState(() => getClinicConfig());
  const [clinicData, setClinicData] = useState(() => getClinicData());
  const [showChatModal, setShowChatModal] = useState(false);

  // Use appointments from admin data
  const appointments = clinicData.appointments || [];
  const basePatients = clinicData.patients || []; // <-- single base source

  // Modal and form states
  const [showPrescriptionModal, setShowPrescriptionModal] = useState(false);
  const [showConsultationModal, setShowConsultationModal] = useState(false);
  const [selectedPatient, setSelectedPatient] = useState(null);
  const [prescription, setPrescription] = useState('');
  const [consultationNotes, setConsultationNotes] = useState('');
  const [showMedicalModal, setShowMedicalModal] = useState(false);
  const [medicalRecordPatient, setMedicalRecordPatient] = useState(null);
  const [showPatientDetailsModal, setShowPatientDetailsModal] = useState(false);
  const [showEditComplaintModal, setShowEditComplaintModal] = useState(false);
  const [editingComplaint, setEditingComplaint] = useState('');
  const [editingComplaintDate, setEditingComplaintDate] = useState('');
  const [showAssignmentModal, setShowAssignmentModal] = useState(false);

  // New appointment scheduling states
  const [showScheduleModal, setShowScheduleModal] = useState(false);
  const [scheduleForm, setScheduleForm] = useState({
    patientId: '',
    patientName: '',
    patientEmail: '',
    patientPhone: '',
    date: '',
    time: '',
    type: '',
    chiefComplaint: '',
    priority: 'normal'
  });

  // Current doctor info
  const currentDoctor = {
    id: 'D001',
    name: 'Dr. Emily Smith',
    role: 'doctor',
    specialty: 'Cardiology'
  };

  // Listen for admin portal updates
  useEffect(() => {
    const handleDataUpdate = () => {
      setConfig(getClinicConfig());
      setClinicData(getClinicData());
    };
    window.addEventListener('clinic-data-updated', handleDataUpdate);
    return () => window.removeEventListener('clinic-data-updated', handleDataUpdate);
  }, []);

  // Show banner if set by admin
  const BannerComponent = () => {
    if (!config.broadcastBanner) return null;
    return (
      <Alert variant="info" className="mb-3 text-center">
        {config.broadcastBanner}
      </Alert>
    );
  };

  const getStatusBadge = (status) => {
    const variants = {
      scheduled: 'primary',
      'in-progress': 'warning',
      completed: 'success',
      cancelled: 'danger'
    };
    return <Badge bg={variants[status] || 'secondary'}>{String(status).toUpperCase()}</Badge>;
  };

  const getPriorityBadge = (priority) => {
    const variants = { normal: 'secondary', urgent: 'danger', high: 'warning' };
    return <Badge bg={variants[priority] || 'secondary'}>{String(priority).toUpperCase()}</Badge>;
  };

  const getLabStatusBadge = (status, critical) => {
    if (critical && status === 'pending_review') return <Badge bg="danger">CRITICAL - PENDING REVIEW</Badge>;
    if (status === 'pending_review') return <Badge bg="warning">PENDING REVIEW</Badge>;
    return <Badge bg="success">REVIEWED</Badge>;
  };

  // --- Ensure at least one pending lab exists (adds a sample to the first patient if needed) ---
  const ensureSamplePendingLab = (list) => {
    if (!Array.isArray(list) || list.length === 0) return list || [];
    const hasAnyPending = list.some(p => p.labResults?.some(l => l.status === 'pending_review'));
    if (hasAnyPending) return list;

    const sampleLab = {
      test: 'HbA1c',
      date: new Date().toISOString().slice(0, 10),
      status: 'pending_review',
      critical: false,
      summary: 'Awaiting analyzer verification.',
      orderedBy: 'Dr. Emily Smith',
      components: [{ name: 'HbA1c', value: null, unit: '%', range: '4.0  5.6%' }]
    };

    const clone = JSON.parse(JSON.stringify(list));
    clone[0] = {
      ...clone[0],
      labResults: Array.isArray(clone[0].labResults)
        ? [sampleLab, ...clone[0].labResults]
        : [sampleLab]
    };
    return clone;
  };

  const patients = ensureSamplePendingLab(basePatients); // <-- single, final patients value

  // Shared Lab modal state
  const [showLabModal, setShowLabModal] = useState(false);
  const [selectedLab, setSelectedLab] = useState(null);

  const pendingLabs = patients.flatMap(p =>
    (p.labResults || [])
      .filter(l => l.status === 'pending_review')
      .map(l => ({ ...l, patientName: p.name }))
  );

  const openFirstPendingLab = () => {
    if (pendingLabs.length === 0) return;
    setSelectedLab(pendingLabs[0]);
    setShowLabModal(true);
  };

  const openLabFromRecord = (patientName, lab) => {
    setSelectedLab({ ...lab, patientName });
    setShowLabModal(true);
  };

  const handleStartConsultation = (appointment) => {
    setSelectedPatient(appointment);
    setShowConsultationModal(true);
  };

  const handlePrescription = (patient) => {
    setSelectedPatient(patient);
    setShowPrescriptionModal(true);
  };

  const handleViewPatientRecords = (patient) => {
    const patientData = {
      id: patient.patientId || patient.id,
      name: patient.patientName || patient.name,
      dob: patient.patientDob || patient.dob || '1990-01-01',
      sex: patient.sex || 'O',
      phone: patient.patientPhone || patient.phone,
      email: patient.patientEmail || patient.email,
      address: patient.address || '123 Main St, Accra, Ghana',
      bloodType: patient.bloodType || 'O+',
      allergies: patient.allergies || []
    };
    setMedicalRecordPatient(patientData);
    setShowMedicalModal(true);
  };

  const handleViewPatientDetails = (patient) => {
    const fullPatientData = patients.find(p => p.id === (patient.patientId || patient.id)) || patient;
    setSelectedPatient(fullPatientData);
    setShowPatientDetailsModal(true);
  };

  const handleEditComplaint = (appointment) => {
    setSelectedPatient(appointment);
    setEditingComplaint(appointment.chiefComplaint || '');
    setEditingComplaintDate(appointment.complaintDate || '');
    setShowEditComplaintModal(true);
  };

  const saveComplaintEdit = () => {
    const updatedAppointments = appointments.map(apt =>
      apt.id === selectedPatient.id
        ? { ...apt, chiefComplaint: editingComplaint, complaintDate: editingComplaintDate }
        : apt
    );
    updateClinicData('appointments', updatedAppointments);
    setShowEditComplaintModal(false);
    setSelectedPatient(null);
    setEditingComplaint('');
    setEditingComplaintDate('');
    alert('Chief complaint updated successfully!');
  };

  const generateZoomLink = () => {
    const randomId = Math.floor(Math.random() * 9000000000 + 1000000000);
    return `https://zoom.us/j/${randomId}`;
  };

  const savePrescription = () => {
    console.log('Saving prescription for', selectedPatient?.patientName || selectedPatient?.name, ':', prescription);
    setPrescription('');
    setShowPrescriptionModal(false);
    setSelectedPatient(null);
    alert('Prescription sent successfully!');
  };

  const saveConsultationNotes = () => {
    console.log('Saving consultation notes for', selectedPatient?.patientName, ':', consultationNotes);
    setConsultationNotes('');
    setShowConsultationModal(false);
    setSelectedPatient(null);
    alert('Consultation notes saved successfully!');
  };

  const handleAssignmentUpdate = () => {
    // Reload patient data after assignments change (placeholder; wire to API if needed)
    console.log('Reloading patient data after assignment update');
  };

  const loadPatientData = () => {
    console.log('Loading patient data...');
  };

  // New unified appointment scheduling function
  const submitSchedule = () => {
    if (!scheduleForm.patientName || !scheduleForm.date || !scheduleForm.time || !scheduleForm.type) {
      alert('Please fill in all required fields');
      return;
    }
    const appointmentData = {
      patientId: scheduleForm.patientId || `P${Date.now()}`,
      patientName: scheduleForm.patientName,
      patientEmail: scheduleForm.patientEmail,
      patientPhone: scheduleForm.patientPhone,
      date: scheduleForm.date,
      time: scheduleForm.time,
      type: scheduleForm.type,
      chiefComplaint: scheduleForm.chiefComplaint,
      priority: scheduleForm.priority,
      status: 'scheduled'
    };
    createAppointment(appointmentData, 'admin');
    setScheduleForm({
      patientId: '', patientName: '', patientEmail: '', patientPhone: '',
      date: '', time: '', type: '', chiefComplaint: '', priority: 'normal'
    });
    setShowScheduleModal(false);
    alert('Appointment scheduled successfully!');
  };

  return (
    <main className="app-content">
      <Container fluid className="py-4">
        <BannerComponent />

        {/* Page Header */}
        <header className="page-header mb-4">
          <Row>
            <Col>
              <h1>Doctor Dashboard</h1>
              <p className="text-muted">Welcome back, Dr. Smith</p>
            </Col>
          </Row>
        </header>

        {/* Statistics */}
        <section aria-label="Statistics" className="mb-4">
          <Row>
            <Col md={3}>
              <Card className="text-center stat-card">
                <Card.Body>
                  <div className="stat-number text-primary h3">
                    {appointments.filter(a => a.status === 'scheduled').length}
                  </div>
                  <div className="stat-label">Today's Appointments</div>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="text-center stat-card">
                <Card.Body>
                  <div className="stat-number text-warning h3">
                    {appointments.filter(a => a.status === 'in-progress').length}
                  </div>
                  <div className="stat-label">In Progress</div>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="text-center stat-card">
                <Card.Body>
                  <div className="stat-number text-success h3">
                    {appointments.filter(a => a.status === 'completed').length}
                  </div>
                  <div className="stat-label">Completed</div>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="text-center stat-card">
                <Card.Body>
                  <div className="stat-number text-info h3">{patients.length}</div>
                  <div className="stat-label">Total Patients</div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </section>

        <Row>
          {/* Appointments */}
          <Col lg={8}>
            <section aria-label="Today's Appointments">
              <Card className="card-light mb-4">
                <Card.Header>
                  <h2 className="h5 mb-0">Today's Appointments</h2>
                </Card.Header>
                <Card.Body>
                  {appointments.length === 0 ? (
                    <div className="text-center py-4 text-muted">
                      <p>No appointments scheduled</p>
                      <small>Appointments will appear here when added by admin or patients</small>
                    </div>
                  ) : (
                    <div className="data-grid">
                      {appointments.map(appointment => {
                        const patient = patients.find(p => p.id === appointment.patientId);
                        const hasAlerts = patient?.alerts?.length > 0;
                        const hasPendingLabs = patient?.labResults?.some(lab => lab.status === 'pending_review');
                        const hasRecentHospitalization = patient?.hospitalizations?.some(h => h.status === 'recent');

                        return (
                          <article key={appointment.id} className="data-card">
                            <div className="data-card-header">
                              <div className="d-flex align-items-center">
                                <div className="data-card-title me-2">{appointment.patientName}</div>
                                {hasRecentHospitalization && <Badge bg="danger" className="me-1">RECENT ADMISSION</Badge>}
                                {hasPendingLabs && <Badge bg="warning">LABS PENDING</Badge>}
                              </div>
                              <time dateTime={`${appointment.date}T${appointment.time}`}>{appointment.time}</time>
                            </div>

                            {hasAlerts && (
                              <Alert variant="warning" className="py-1 px-2 mb-2 small">
                                <strong>Alerts:</strong> {patient.alerts.join(', ')}
                              </Alert>
                            )}

                            <div className="mb-2">
                              <div className="d-flex justify-content-between align-items-start">
                                <div className="flex-grow-1">
                                  <strong>Chief Complaint:</strong>
                                  <div className="text-muted small">{appointment.chiefComplaint || 'No complaint recorded'}</div>
                                  <div className="text-muted" style={{ fontSize: '0.75rem' }}>
                                    Reported: {appointment.complaintDate || 'N/A'}
                                  </div>
                                </div>
                                {config?.roles?.doctor?.canEditComplaints && (
                                  <Button
                                    variant="secondary"
                                    size="sm"
                                    onClick={() => handleEditComplaint(appointment)}
                                    style={{ fontSize: '0.75rem', padding: '0.25rem 0.5rem' }}
                                  >
                                    Edit
                                  </Button>
                                )}
                              </div>
                            </div>

                            <div className="data-card-meta">
                              <div className="data-card-field">
                                <span className="data-card-label">Email</span>
                                <span className="data-card-value">{appointment.patientEmail}</span>
                              </div>
                              <div className="data-card-field">
                                <span className="data-card-label">Last Visit</span>
                                <span className="data-card-value">{patient?.lastVisit || 'N/A'}</span>
                              </div>
                              <div className="data-card-field">
                                <span className="data-card-label">Type</span>
                                <span className="data-card-value">{appointment.type}</span>
                              </div>
                              <div className="data-card-field">
                                <span className="data-card-label">Priority</span>
                                <span className="data-card-value">{getPriorityBadge(appointment.priority || 'normal')}</span>
                              </div>
                              <div className="data-card-field">
                                <span className="data-card-label">Status</span>
                                <span className="data-card-value">{getStatusBadge(appointment.status)}</span>
                              </div>
                            </div>

                            <div className="data-card-actions mt-3">
                              <Button
                                variant="primary"
                                size="sm"
                                className="me-2"
                                onClick={() => handleStartConsultation(appointment)}
                                disabled={appointment.status === 'completed'}
                                aria-label={`${appointment.status === 'in-progress' ? 'Continue' : 'Start'} consultation with ${appointment.patientName}`}
                              >
                                {appointment.status === 'in-progress' ? 'Continue' : 'Start'}
                              </Button>
                              {config?.featureFlags?.enableMedicalRecords && (
                                <Button
                                  variant="outline-primary"
                                  size="sm"
                                  className="me-2"
                                  onClick={() => handleViewPatientRecords(appointment)}
                                  aria-label={`View medical records for ${appointment.patientName}`}
                                >
                                  Records
                                </Button>
                              )}
                              <Button
                                variant="secondary"
                                size="sm"
                                className="me-2"
                                onClick={() => handleViewPatientDetails(appointment)}
                                aria-label={`View patient details for ${appointment.patientName}`}
                              >
                                Details
                              </Button>
                              {config?.roles?.doctor?.canPrescribe && (
                                <Button
                                  variant="outline-primary"
                                  size="sm"
                                  onClick={() => handlePrescription(appointment)}
                                  aria-label={`Write prescription for ${appointment.patientName}`}
                                >
                                  Prescribe
                                </Button>
                              )}
                            </div>
                          </article>
                        );
                      })}
                    </div>
                  )}
                </Card.Body>
              </Card>
            </section>
          </Col>

          {/* Sidebar */}
          <Col lg={4}>
            {/* Quick Actions */}
            <section aria-label="Quick Actions">
              <Card className="mb-4">
                <Card.Header>
                  <h2 className="h5 mb-0">Quick Actions</h2>
                </Card.Header>
                <Card.Body>
                  <nav className="quick-actions">
                    <Button
                      variant="primary"
                      className="btn-emergency w-100 mb-2"
                      onMouseDown={(e) => e.preventDefault()}
                      onClick={(e) => { e.currentTarget.blur(); window.open(generateZoomLink(), '_blank'); }}
                      aria-label="Start emergency video consultation"
                    >
                      Start Emergency Consultation
                    </Button>

                    <Button
                      className="w-100 mb-2 btn-manage-patients"
                      variant="primary"
                      onMouseDown={(e) => e.preventDefault()}
                      onClick={(e) => {
                        e.currentTarget.blur();
                        setShowAssignmentModal(true);
                      }}
                      aria-label="Manage Patients"
                    >
                      Manage Patients
                    </Button>

                    <Button
                      variant="primary"
                      className="w-100 mb-2"
                      onMouseDown={(e) => e.preventDefault()}
                      onClick={(e) => { e.currentTarget.blur(); openFirstPendingLab(); }}
                    >
                      Review Pending Labs ({pendingLabs.length})
                    </Button>

                    <Button
                      variant="outline-primary"
                      className="btn-schedule w-100 mb-2"
                      onMouseDown={(e) => e.preventDefault()}
                      onClick={(e) => { e.currentTarget.blur(); setShowScheduleModal(true); }}
                      aria-label="Schedule new appointment"
                    >
                      Schedule Appointment
                    </Button>

                    {config?.featureFlags?.enableChat && (
                      <Button
                        variant="secondary"
                        className="btn-send-message w-100"
                        onMouseDown={(e) => e.preventDefault()}
                        onClick={(e) => { e.currentTarget.blur(); setShowChatModal(true); }}
                        aria-label="Send message to patient"
                      >
                        Send Patient Message
                      </Button>
                    )}
                  </nav>
                </Card.Body>
              </Card>
            </section>

            {/* Recent Patients */}
            <section aria-label="Recent Patients">
              <Card className="card-dark bg-dark text-white">
                <Card.Header>
                  <h2 className="h5 mb-0">Recent Patients</h2>
                </Card.Header>
                <Card.Body>
                  {patients.length === 0 ? (
                    <div className="text-center py-4 text-white-50">
                      <p>No patients available</p>
                      <small>Patients will appear here when added by admin</small>
                    </div>
                  ) : (
                    <div className="patient-list">
                      {patients.map(patient => (
                        <article key={patient.id} className="patient-card border-bottom pb-3 mb-3">
                          <div className="d-flex justify-content-between align-items-start">
                            <div className="patient-info">
                              <div className="d-flex align-items-center mb-1">
                                <h3 className="h6 mb-0 me-2">{patient.name}</h3>
                                {patient.hospitalizations?.some(h => h.status === 'recent') && (
                                  <Badge bg="danger" className="small">ADMITTED</Badge>
                                )}
                              </div>
                              <dl className="mb-2">
                                <dt className="sr-only">Age</dt>
                                <dd className="text-white-50 mb-0">Age: {patient.age}</dd>
                                <dt className="sr-only">Last Visit</dt>
                                <dd className="text-white-50 mb-0">
                                  Last: <time dateTime={patient.lastVisit}>{patient.lastVisit || 'N/A'}</time>
                                </dd>
                                {patient.labResults?.filter(lab => lab.status === 'pending_review').length > 0 && (
                                  <dd className="text-warning mb-0">
                                     {patient.labResults.filter(lab => lab.status === 'pending_review').length} labs pending review
                                  </dd>
                                )}
                              </dl>
                              <Badge bg="info" className="condition-badge">{patient.condition}</Badge>
                            </div>
                            <div>
                              {config?.featureFlags?.enableMedicalRecords && (
                                <Button
                                  variant="outline-primary"
                                  size="sm"
                                  className="me-1"
                                  onClick={() => handleViewPatientRecords(patient)}
                                  aria-label={`View records for ${patient.name}`}
                                >
                                  Records
                                </Button>
                              )}
                              {config?.roles?.doctor?.canPrescribe && (
                                <Button
                                  variant="secondary"
                                  size="sm"
                                  onClick={() => handlePrescription(patient)}
                                  aria-label={`Prescribe for ${patient.name}`}
                                >
                                  Prescribe
                                </Button>
                              )}
                            </div>
                          </div>
                        </article>
                      ))}
                    </div>
                  )}
                </Card.Body>
              </Card>
            </section>
          </Col>
        </Row>

        {/* Medical Record Module */}
        {config?.featureFlags?.enableMedicalRecords && showMedicalModal && (
          <MedicalRecordModule
            show={showMedicalModal}
            onHide={() => { setShowMedicalModal(false); setMedicalRecordPatient(null); }}
            currentUser={currentDoctor}
            patient={medicalRecordPatient}
            onOpenLab={openLabFromRecord}  // optional: lets record UI open the same modal
          />
        )}

        {/* Schedule Appointment Modal */}
        <Modal show={showScheduleModal} onHide={() => setShowScheduleModal(false)} size="lg">
          <Modal.Header closeButton>
            <Modal.Title>Schedule New Appointment</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Patient Name *</Form.Label>
                    <Form.Control
                      value={scheduleForm.patientName}
                      onChange={e => setScheduleForm({ ...scheduleForm, patientName: e.target.value })}
                      placeholder="Enter patient name"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Patient Email</Form.Label>
                    <Form.Control
                      type="email"
                      value={scheduleForm.patientEmail}
                      onChange={e => setScheduleForm({ ...scheduleForm, patientEmail: e.target.value })}
                      placeholder="Enter patient email"
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Patient Phone</Form.Label>
                    <Form.Control
                      type="tel"
                      value={scheduleForm.patientPhone}
                      onChange={e => setScheduleForm({ ...scheduleForm, patientPhone: e.target.value })}
                      placeholder="Enter patient phone"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Patient ID (Optional)</Form.Label>
                    <Form.Control
                      value={scheduleForm.patientId}
                      onChange={e => setScheduleForm({ ...scheduleForm, patientId: e.target.value })}
                      placeholder="Auto-generated if empty"
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Date *</Form.Label>
                    <Form.Control
                      type="date"
                      value={scheduleForm.date}
                      onChange={e => setScheduleForm({ ...scheduleForm, date: e.target.value })}
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Time *</Form.Label>
                    <Form.Control
                      type="time"
                      value={scheduleForm.time}
                      onChange={e => setScheduleForm({ ...scheduleForm, time: e.target.value })}
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Appointment Type *</Form.Label>
                    <Form.Select
                      value={scheduleForm.type}
                      onChange={e => setScheduleForm({ ...scheduleForm, type: e.target.value })}
                    >
                      <option value="">Select type...</option>
                      {config?.appointmentTypes?.map(type => (
                        <option key={type} value={type}>{type}</option>
                      ))}
                    </Form.Select>
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group className="mb-3">
                    <Form.Label>Priority</Form.Label>
                    <Form.Select
                      value={scheduleForm.priority}
                      onChange={e => setScheduleForm({ ...scheduleForm, priority: e.target.value })}
                    >
                      <option value="normal">Normal</option>
                      <option value="high">High</option>
                      <option value="urgent">Urgent</option>
                    </Form.Select>
                  </Form.Group>
                </Col>
              </Row>
              <Form.Group className="mb-3">
                <Form.Label>Chief Complaint</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={3}
                  value={scheduleForm.chiefComplaint}
                  onChange={e => setScheduleForm({ ...scheduleForm, chiefComplaint: e.target.value })}
                  placeholder="Enter patient's chief complaint..."
                />
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowScheduleModal(false)}>Cancel</Button>
            <Button variant="primary" onClick={submitSchedule}>Schedule Appointment</Button>
          </Modal.Footer>
        </Modal>

        {/* Patient Details Modal */}
        <Modal
          show={showPatientDetailsModal}
          onHide={() => setShowPatientDetailsModal(false)}
          size="xl"
          aria-labelledby="patient-details-modal-title"
        >
          <Modal.Header closeButton>
            <Modal.Title id="patient-details-modal-title">
              Patient Details - {selectedPatient?.name || selectedPatient?.patientName}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {selectedPatient && (
              <Accordion defaultActiveKey={['0', '1']} alwaysOpen>
                {/* Alerts */}
                {selectedPatient.alerts?.length > 0 && (
                  <Accordion.Item eventKey="alerts">
                    <Accordion.Header> Active Alerts</Accordion.Header>
                    <Accordion.Body>
                      {selectedPatient.alerts.map((alert, index) => (
                        <Alert key={index} variant="warning">{alert}</Alert>
                      ))}
                    </Accordion.Body>
                  </Accordion.Item>
                )}

                {/* Recent Hospitalization */}
                {selectedPatient.hospitalizations?.length > 0 && (
                  <Accordion.Item eventKey="hospitalization">
                    <Accordion.Header> Recent Hospitalizations</Accordion.Header>
                    <Accordion.Body>
                      {selectedPatient.hospitalizations.map((admission, index) => (
                        <Card key={index} className="mb-2">
                          <Card.Body>
                            <div className="d-flex justify-content-between align-items-start">
                              <div>
                                <h6 className="text-danger">{admission.diagnosis}</h6>
                                <p className="mb-1"><strong>Admission:</strong> {admission.admissionDate} - {admission.dischargeDate}</p>
                                <p className="mb-1"><strong>Department:</strong> {admission.department} | <strong>Length:</strong> {admission.length}</p>
                              </div>
                              <Badge bg="danger">RECENT</Badge>
                            </div>
                          </Card.Body>
                        </Card>
                      ))}
                    </Accordion.Body>
                  </Accordion.Item>
                )}

                {/* Lab Results */}
                <Accordion.Item eventKey="0">
                  <Accordion.Header> Lab Results</Accordion.Header>
                  <Accordion.Body>
                    {selectedPatient.labResults?.length > 0 ? (
                      <Table striped bordered hover>
                        <thead>
                          <tr>
                            <th>Test</th>
                            <th>Date</th>
                            <th>Summary</th>
                            <th>Review Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          {selectedPatient.labResults.map((lab, index) => (
                            <tr key={index} className={lab.critical ? 'table-danger' : ''}>
                              <td>
                                <strong>{lab.test}</strong>
                                {lab.critical && <Badge bg="danger" className="ms-2">CRITICAL</Badge>}
                              </td>
                              <td>{lab.date}</td>
                              <td>{lab.summary}</td>
                              <td>{getLabStatusBadge(lab.status, lab.critical)}</td>
                            </tr>
                          ))}
                        </tbody>
                      </Table>
                    ) : (
                      <p className="text-muted">No lab results available</p>
                    )}
                  </Accordion.Body>
                </Accordion.Item>

                {/* Recent Visits */}
                <Accordion.Item eventKey="1">
                  <Accordion.Header> Recent Visits</Accordion.Header>
                  <Accordion.Body>
                    {selectedPatient.recentVisits?.length > 0 ? (
                      <Table striped bordered hover>
                        <thead>
                          <tr>
                            <th>Date</th>
                            <th>Type</th>
                            <th>Diagnosis/Notes</th>
                          </tr>
                        </thead>
                        <tbody>
                          {selectedPatient.recentVisits.map((visit, index) => (
                            <tr key={index}>
                              <td>{visit.date}</td>
                              <td>
                                <Badge bg={visit.type === 'Emergency' ? 'danger' : 'primary'}>
                                  {visit.type}
                                </Badge>
                              </td>
                              <td>{visit.diagnosis}</td>
                            </tr>
                          ))}
                        </tbody>
                      </Table>
                    ) : (
                      <p className="text-muted">No recent visits recorded</p>
                    )}
                  </Accordion.Body>
                </Accordion.Item>

                {/* Basic Info */}
                <Accordion.Item eventKey="2">
                  <Accordion.Header> Patient Information</Accordion.Header>
                  <Accordion.Body>
                    <Row>
                      <Col md={6}>
                        <p><strong>DOB:</strong> {selectedPatient.dob}</p>
                        <p><strong>Blood Type:</strong> {selectedPatient.bloodType}</p>
                        <p><strong>Phone:</strong> {selectedPatient.phone}</p>
                      </Col>
                      <Col md={6}>
                        <p><strong>Email:</strong> {selectedPatient.email}</p>
                        <p><strong>Allergies:</strong> {selectedPatient.allergies?.join(', ') || 'None'}</p>
                        <p><strong>Current Conditions:</strong> {selectedPatient.condition}</p>
                      </Col>
                    </Row>
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
            )}
          </Modal.Body>
          <Modal.Footer>
            {config?.roles?.doctor?.canPrescribe && (
              <Button variant="primary" onClick={() => handlePrescription(selectedPatient)}>
                Write Prescription
              </Button>
            )}
            {config?.featureFlags?.enableTelehealth && (
              <Button variant="outline-primary" onClick={() => window.open(generateZoomLink(), '_blank')}>
                Start Video Call
              </Button>
            )}
            <Button variant="secondary" onClick={() => setShowPatientDetailsModal(false)}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>

        {/* Prescription Modal */}
        {config?.roles?.doctor?.canPrescribe && (
          <Modal
            show={showPrescriptionModal}
            onHide={() => setShowPrescriptionModal(false)}
            size="lg"
            aria-labelledby="prescription-modal-title"
          >
            <Modal.Header closeButton>
              <Modal.Title id="prescription-modal-title">
                Write Prescription - {selectedPatient?.patientName || selectedPatient?.name}
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form>
                <Form.Group className="mb-3">
                  <Form.Label htmlFor="prescription-details">Prescription Details</Form.Label>
                  <Form.Control
                    id="prescription-details"
                    as="textarea"
                    rows={6}
                    value={prescription}
                    onChange={(e) => setPrescription(e.target.value)}
                    placeholder="Enter medication, dosage, frequency, and instructions..."
                    aria-describedby="prescription-help"
                  />
                  <Alert variant="info" className="mt-2" id="prescription-help">
                    <small>This prescription will be sent to the pharmacy and patient automatically.</small>
                  </Alert>
                </Form.Group>
              </Form>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={() => setShowPrescriptionModal(false)}>
                Cancel
              </Button>
              <Button variant="primary" onClick={savePrescription}>
                Send Prescription
              </Button>
            </Modal.Footer>
          </Modal>
        )}

        {/* Shared Lab Result Modal */}
        <LabResultModal
          show={showLabModal}
          onHide={() => { setShowLabModal(false); setSelectedLab(null); }}
          lab={selectedLab}
        />

        {/* Consultation Modal */}
        <Modal
          show={showConsultationModal}
          onHide={() => setShowConsultationModal(false)}
          size="lg"
          aria-labelledby="consultation-modal-title"
        >
          <Modal.Header closeButton>
            <Modal.Title id="consultation-modal-title">
              Consultation - {selectedPatient?.patientName}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="mb-3">
              {config?.featureFlags?.enableTelehealth && (
                <Button
                  variant="primary"
                  className="me-2"
                  onClick={() => window.open(generateZoomLink(), '_blank')}
                  aria-label="Start video call with patient"
                >
                  Start Video Call
                </Button>
              )}
              {config?.featureFlags?.enableMedicalRecords && (
                <Button
                  variant="outline-primary"
                  onClick={() => handleViewPatientRecords(selectedPatient)}
                  aria-label="View patient medical records"
                >
                  View Patient Records
                </Button>
              )}
            </div>
            <Form>
              <Form.Group className="mb-3">
                <Form.Label htmlFor="consultation-notes">Consultation Notes</Form.Label>
                <Form.Control
                  id="consultation-notes"
                  as="textarea"
                  rows={6}
                  value={consultationNotes}
                  onChange={(e) => setConsultationNotes(e.target.value)}
                  placeholder="Enter consultation notes, observations, diagnosis..."
                />
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowConsultationModal(false)}>
              Cancel
            </Button>
            <Button variant="primary" onClick={saveConsultationNotes}>
              Save Notes
            </Button>
          </Modal.Footer>
        </Modal>

        {/* Chat Module */}
        {config?.featureFlags?.enableChat && (
          <ChatModule
            show={showChatModal}
            onHide={() => setShowChatModal(false)}
            currentUser={currentDoctor}
          />
        )}

        {/* Patient Assignment */}
        <PatientAssignmentModule
          show={showAssignmentModal}
          onHide={() => setShowAssignmentModal(false)}
          currentUser={{ id: 'N001', role: 'nurse', name: 'Nurse Johnson' }}
          onAssignmentUpdate={handleAssignmentUpdate}
          onViewDetails={(p) => handleViewPatientDetails(p)}
          onViewRecord={(p) => handleViewPatientRecords(p)}
          enableRecordQuickOpen={true}
        />

        {/* Edit Complaint Modal */}
        {config?.roles?.doctor?.canEditComplaints && (
          <Modal
            show={showEditComplaintModal}
            onHide={() => setShowEditComplaintModal(false)}
            size="lg"
            aria-labelledby="edit-complaint-modal-title"
          >
            <Modal.Header closeButton>
              <Modal.Title id="edit-complaint-modal-title">
                Edit Chief Complaint - {selectedPatient?.patientName}
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form>
                <Form.Group className="mb-3">
                  <Form.Label htmlFor="complaint-date">Date Reported</Form.Label>
                  <Form.Control
                    id="complaint-date"
                    type="date"
                    value={editingComplaintDate}
                    onChange={(e) => setEditingComplaintDate(e.target.value)}
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Label htmlFor="complaint-text">Chief Complaint</Form.Label>
                  <Form.Control
                    id="complaint-text"
                    as="textarea"
                    rows={4}
                    value={editingComplaint}
                    onChange={(e) => setEditingComplaint(e.target.value)}
                    placeholder="Enter patient's chief complaint..."
                  />
                </Form.Group>
              </Form>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={() => setShowEditComplaintModal(false)}>
                Cancel
              </Button>
              <Button variant="primary" onClick={saveComplaintEdit}>
                Save Changes
              </Button>
            </Modal.Footer>
          </Modal>
        )}
      </Container>
    </main>
  );
};

export default DoctorDashboard;
