// components/GlobalBanner.js
import React, { useState, useEffect } from 'react';
import { Alert } from 'react-bootstrap';
import { getClinicConfig } from '../config/dataStore';

const GlobalBanner = () => {
  const [config, setConfig] = useState(() => getClinicConfig());

  // Listen for admin portal updates
  useEffect(() => {
    const handleDataUpdate = () => {
      setConfig(getClinicConfig());
    };

    window.addEventListener('clinic-data-updated', handleDataUpdate);
    return () => window.removeEventListener('clinic-data-updated', handleDataUpdate);
  }, []);

  // Don't render anything if no banner is set
  if (!config.broadcastBanner) return null;

  return (
    <Alert variant="info" className="mb-0 text-center border-0 rounded-0">
      <strong> {config.broadcastBanner}</strong>
    </Alert>
  );
};

export default GlobalBanner;