import React, { useMemo, useState } from 'react';
import { Modal, Button, Form, InputGroup } from 'react-bootstrap';
import PatientListView from './PatientListView';

export default function PatientSearchModal({
  show,
  onHide,
  patients = [],
  onOpenRecord, // (patient) => void
}) {
  const [q, setQ] = useState('');

  const filtered = useMemo(() => {
    const needle = q.trim().toLowerCase();
    if (!needle) return patients;
    return patients.filter(p => {
      const hay = [
        p.id, p.name, p.room, p.department, p.dept,
        p.condition, p.chiefComplaint, p.doctor,
      ]
        .filter(Boolean)
        .join(' ')
        .toLowerCase();
      return hay.includes(needle);
    });
  }, [patients, q]);

  return (
    <Modal show={show} onHide={onHide} size="lg" backdrop="static">
      <Modal.Header closeButton>
        <Modal.Title>Find Patient Record</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <InputGroup className="mb-3">
          <InputGroup.Text></InputGroup.Text>
          <Form.Control
            placeholder="Search by name, ID, room, dept, condition"
            value={q}
            onChange={(e) => setQ(e.target.value)}
            autoFocus
          />
          {q && (
            <Button variant="outline-secondary" onClick={() => setQ('')}>
              Clear
            </Button>
          )}
        </InputGroup>

        <PatientListView
          patients={filtered}
          onViewRecord={(p) => onOpenRecord?.(p)}
        />
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}
