// src/components/NurseDashboard.js
import PatientAssignmentModule from './PatientAssignmentModule';
import React, { useState, useEffect } from 'react';
import {
  Container, Row, Col, Card, Button, Table, Badge, Form,
  Modal, Alert, InputGroup
} from 'react-bootstrap';
import MedicalRecordModule from './MedicalRecordModule';
import PatientSearchModal from './PatientSearchModal';

const NurseDashboard = () => {
  const [patients, setPatients] = useState([]);
  const [medications, setMedications] = useState([]);
  const [showVitalsModal, setShowVitalsModal] = useState(false);
  const [showMedicationModal, setShowMedicationModal] = useState(false);
  const [showNotesModal, setShowNotesModal] = useState(false);

  // Medical record + details modals
  const [showMedicalModal, setShowMedicalModal] = useState(false);
  const [medicalRecordPatient, setMedicalRecordPatient] = useState(null);
  const [showPatientDetailsModal, setShowPatientDetailsModal] = useState(false);

  const [selectedPatient, setSelectedPatient] = useState(null);

  const [newVitals, setNewVitals] = useState({
    bloodPressure: '',
    heartRate: '',
    temperature: '',
    oxygenSaturation: '',
    respiratoryRate: '',
    weight: '',
    height: ''
  });
  const [nursingNotes, setNursingNotes] = useState('');
  const [showAssignmentModal, setShowAssignmentModal] = useState(false);

  // Search modal state
  const [showPatientSearch, setShowPatientSearch] = useState(false);

  // Notes per patient for shift report
  const [notesByPatient, setNotesByPatient] = useState({});

  // ====== SHIFT REPORT STATE ======
  const [showShiftModal, setShowShiftModal] = useState(false);
  const [shiftStart, setShiftStart] = useState(() => {
    const d = new Date();
    d.setHours(7, 0, 0, 0); // default 7:00 AM
    return d.toISOString().slice(0, 16);
  });
  const [shiftEnd, setShiftEnd] = useState(() => {
    const d = new Date();
    d.setHours(19, 0, 0, 0); // default 7:00 PM
    return d.toISOString().slice(0, 16);
  });
  const [shiftReport, setShiftReport] = useState('');

  // Mock data - replace with API calls later
  useEffect(() => {
    const mockPatients = [
      {
        id: 1,
        name: 'John Doe',
        age: 45,
        room: 'A101',
        condition: 'Post-Op Recovery',
        admissionDate: '2025-09-09',
        priority: 'high',
        doctor: 'Dr. Smith',
        allergies: ['Penicillin'],
        status: 'stable',
        lastVitalsCheck: '08:00 AM',
        dob: '1980-03-12',
        sex: 'M',
        phone: '+233-20-555-0101',
        email: 'john.doe@email.com',
        address: '12 Palm St, Accra, Ghana',
        bloodType: 'A+',
      },
      {
        id: 2,
        name: 'Jane Smith',
        age: 32,
        room: 'B205',
        condition: 'Diabetes Management',
        admissionDate: '2025-09-10',
        priority: 'normal',
        doctor: 'Dr. Johnson',
        allergies: [],
        status: 'monitoring',
        lastVitalsCheck: '07:30 AM',
        dob: '1993-11-05',
        sex: 'F',
        phone: '+233-24-555-0102',
        email: 'jane.smith@email.com',
        address: '45 Ridge Rd, Accra, Ghana',
        bloodType: 'O+',
      },
      {
        id: 3,
        name: 'Bob Johnson',
        age: 67,
        room: 'C301',
        condition: 'Cardiac Monitoring',
        admissionDate: '2025-09-08',
        priority: 'urgent',
        doctor: 'Dr. Brown',
        allergies: ['Aspirin', 'Latex'],
        status: 'critical',
        lastVitalsCheck: '08:30 AM',
        dob: '1958-06-20',
        sex: 'M',
        phone: '+233-26-555-0103',
        email: 'bob.johnson@email.com',
        address: '78 Market Ave, Accra, Ghana',
        bloodType: 'B-',
      }
    ];

    const mockMedications = [
      {
        id: 1,
        patientId: 1,
        patientName: 'John Doe',
        medication: 'Morphine 5mg',
        time: '09:00 AM',
        status: 'pending',
        route: 'IV',
        instructions: 'For pain management'
      },
      {
        id: 2,
        patientId: 2,
        patientName: 'Jane Smith',
        medication: 'Insulin 10 units',
        time: '08:00 AM',
        status: 'administered',
        route: 'Subcutaneous',
        instructions: 'Before breakfast'
      },
      {
        id: 3,
        patientId: 3,
        patientName: 'Bob Johnson',
        medication: 'Lisinopril 10mg',
        time: '09:30 AM',
        status: 'overdue',
        route: 'Oral',
        instructions: 'With water'
      }
    ];

    setPatients(mockPatients);
    setMedications(mockMedications);
  }, []);

  const getStatusBadge = (status) => {
    const variants = {
      stable: 'success',
      monitoring: 'warning',
      critical: 'danger',
      recovering: 'info'
    };
    return <Badge bg={variants[status] || 'secondary'}>{status}</Badge>;
  };

  const getPriorityBadge = (priority) => {
    const variants = { normal: 'secondary', high: 'warning', urgent: 'danger' };
    return <Badge bg={variants[priority] || 'secondary'}>{priority}</Badge>;
  };

  const getMedicationStatusBadge = (status) => {
    const variants = {
      pending: 'warning',
      administered: 'success',
      overdue: 'danger',
      cancelled: 'secondary'
    };
    return <Badge bg={variants[status] || 'secondary'}>{status}</Badge>;
  };

  const handleRecordVitals = (patient) => {
    setSelectedPatient(patient);
    setShowVitalsModal(true);
  };

  const handleMedicationAdmin = (medication) => {
    setSelectedPatient(medication);
    setShowMedicationModal(true);
  };

  const handleNursingNotes = (patient) => {
    setSelectedPatient(patient);
    setShowNotesModal(true);
  };

  const handleAssignmentUpdate = () => {
    console.log('Reloading patient data after assignment update');
  };

  const loadPatientData = () => {
    console.log('Loading patient data...');
  };

  const handleViewPatientRecords = (patient) => {
    const patientData = {
      id: patient.patientId || patient.id,
      name: patient.patientName || patient.name,
      dob: patient.patientDob || patient.dob || '1990-01-01',
      sex: patient.sex || 'O',
      phone: patient.patientPhone || patient.phone,
      email: patient.patientEmail || patient.email,
      address: patient.address || '123 Main St, Accra, Ghana',
      bloodType: patient.bloodType || 'O+',
      allergies: patient.allergies || []
    };
    setMedicalRecordPatient(patientData);
    setShowMedicalModal(true);
  };

  const handleViewPatientDetails = (patient) => {
    const fullPatientData = patients.find(p => p.id === (patient.patientId || patient.id)) || patient;
    setSelectedPatient(fullPatientData);
    setShowPatientDetailsModal(true);
  };

  const openRecordFor = (patient) => {
    const p = patient || patients[0] || null;
    if (!p) return;
    setSelectedPatient(p);
    setShowMedicalModal(true);
  };

  const saveVitals = () => {
    console.log('Saving vitals for', selectedPatient?.name, ':', newVitals);
    setNewVitals({
      bloodPressure: '',
      heartRate: '',
      temperature: '',
      oxygenSaturation: '',
      respiratoryRate: '',
      weight: '',
      height: ''
    });
    setShowVitalsModal(false);
    setSelectedPatient(null);
  };

  const administerMedication = () => {
    console.log('Administering medication:', selectedPatient);
    setMedications(prev =>
      prev.map(med =>
        med.id === selectedPatient.id ? { ...med, status: 'administered' } : med
      )
    );
    setShowMedicationModal(false);
    setSelectedPatient(null);
  };

  const saveNursingNotes = () => {
    if (selectedPatient && nursingNotes.trim()) {
      setNotesByPatient(prev => {
        const list = prev[selectedPatient.id] || [];
        return {
          ...prev,
          [selectedPatient.id]: [
            ...list,
            { timestamp: new Date().toISOString(), text: nursingNotes.trim() }
          ]
        };
      });
    }
    console.log('Saving nursing notes for', selectedPatient?.name, ':', nursingNotes);
    setNursingNotes('');
    setShowNotesModal(false);
    setSelectedPatient(null);
  };

  const getVitalsStatus = (lastCheck) => {
    const now = new Date();
    const [time, period] = lastCheck.split(' ');
    const [hours] = time.split(':');
    let checkHour = parseInt(hours, 10);
    if (period === 'PM' && checkHour !== 12) checkHour += 12;
    if (period === 'AM' && checkHour === 12) checkHour = 0;
    const hoursElapsed = now.getHours() - checkHour;
    return hoursElapsed > 4 ? 'overdue' : 'current';
  };

  const baseRecordPatient = medicalRecordPatient || selectedPatient;
  const patientForRecord = baseRecordPatient
    ? {
        id: String(baseRecordPatient.id ?? 'UNK'),
        name: baseRecordPatient.name ?? 'Unknown',
        dob: baseRecordPatient.dob ?? '1990-01-01',
        sex: baseRecordPatient.sex ?? 'O',
        phone: baseRecordPatient.phone ?? '',
        email: baseRecordPatient.email ?? '',
        address: baseRecordPatient.address ?? '',
        bloodType: baseRecordPatient.bloodType ?? '',
        allergies: baseRecordPatient.allergies ?? [],
      }
    : null;

  // ====== SHIFT REPORT: helpers ======
  const fmtDateTime = (isoLocal) => {
    try {
      const d = new Date(isoLocal);
      return d.toLocaleString();
    } catch {
      return isoLocal;
    }
  };

  const buildShiftReport = () => {
    const now = new Date();
    const header =
`NURSE SHIFT REPORT
Generated: ${now.toLocaleString()}
Shift Window: ${fmtDateTime(shiftStart)}    ${fmtDateTime(shiftEnd)}
Nurse: Nurse Johnson
------------------------------------------------------------`;

    const byPriority = (p) => p.priority || 'normal';
    const patientsSorted = [...patients].sort((a, b) => {
      const order = { urgent: 0, high: 1, normal: 2 };
      return (order[byPriority(a)] ?? 3) - (order[byPriority(b)] ?? 3);
    });

    const patientLines = patientsSorted.map(p => {
      const vitals = getVitalsStatus(p.lastVitalsCheck);
      const notesList = notesByPatient[p.id] || [];
      const lastNote = notesList.length ? ` | Last Note: "${notesList[notesList.length - 1].text}"` : '';
      return `- ${p.name} (Rm ${p.room})  ${p.condition}  Status: ${p.status.toUpperCase()}  Priority: ${p.priority.toUpperCase()}  Last Vitals: ${p.lastVitalsCheck} [${vitals}]${lastNote}`;
    }).join('\n');

    const medsPending = medications.filter(m => m.status === 'pending');
    const medsOverdue = medications.filter(m => m.status === 'overdue');
    const medsDone = medications.filter(m => m.status === 'administered');

    const medSection =
`MEDICATIONS
- Pending: ${medsPending.length}
${medsPending.map(m => `     ${m.patientName}: ${m.medication} @ ${m.time} (${m.route})`).join('\n') || '     None'}
- Overdue: ${medsOverdue.length}
${medsOverdue.map(m => `     ${m.patientName}: ${m.medication} @ ${m.time} (${m.route})`).join('\n') || '     None'}
- Administered: ${medsDone.length}
${medsDone.map(m => `     ${m.patientName}: ${m.medication} @ ${m.time} (${m.route})`).join('\n') || '     None'}`;

    const vitalsOverdue = patients.filter(p => getVitalsStatus(p.lastVitalsCheck) === 'overdue');
    const vitalsSection =
`VITALS OVERDUE (${vitalsOverdue.length})
${vitalsOverdue.map(p => `- ${p.name} (Rm ${p.room})  Last Vitals: ${p.lastVitalsCheck}`).join('\n') || '- None'}`;

    const notesSectionHeader = 'NURSING NOTES (captured this shift)';
    const notesSectionLines = patients.map(p => {
      const items = notesByPatient[p.id] || [];
      if (!items.length) return null;
      const lines = items
        .map(n => `     ${new Date(n.timestamp).toLocaleTimeString()}  ${n.text}`)
        .join('\n');
      return `- ${p.name}\n${lines}`;
    }).filter(Boolean).join('\n');

    const notesSection = `${notesSectionHeader}\n${notesSectionLines || '- No notes recorded.'}`;

    return `${header}

PATIENTS (${patients.length})
${patientLines}

${medSection}

${vitalsSection}

${notesSection}
`;
  };

  const openShiftReport = () => {
    setShiftReport(buildShiftReport());
    setShowShiftModal(true);
  };

  const regenerateReport = () => {
    setShiftReport(buildShiftReport());
  };

  const copyReportToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(shiftReport);
      alert('Shift report copied to clipboard!');
    } catch {
      alert('Copy failed. You can select and copy manually.');
    }
  };

  const downloadReport = () => {
    const blob = new Blob([shiftReport], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    const now = new Date();
    a.href = url;
    a.download = `shift-report-${now.toISOString().slice(0,10)}.txt`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  };

  const printReport = () => {
    const w = window.open('', '_blank', 'noopener,noreferrer');
    if (!w) return;
    w.document.write(`<pre style="font-family: system-ui, -apple-system, Segoe UI, Roboto, monospace; white-space: pre-wrap; line-height:1.4; padding:16px;">${shiftReport.replace(/</g,'&lt;')}</pre>`);
    w.document.close();
    w.focus();
    w.print();
    w.close();
  };

  return (
    <Container fluid className="py-4">
      <Row className="mb-4">
        <Col>
          <h2>Nurse Dashboard</h2>
          <p className="text-muted">Welcome back, Nurse Johnson</p>
        </Col>
      </Row>

      {/* Quick Stats */}
      <Row className="mb-4">
        <Col md={3}>
          <Card className="text-center">
            <Card.Body>
              <h4 className="text-info">{patients.length}</h4>
              <p className="mb-0">Assigned Patients</p>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card className="text-center">
            <Card.Body>
              <h4 className="text-warning">
                {medications.filter(m => m.status === 'pending').length}
              </h4>
              <p className="mb-0">Pending Medications</p>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card className="text-center">
            <Card.Body>
              <h4 className="text-danger">
                {medications.filter(m => m.status === 'overdue').length}
              </h4>
              <p className="mb-0">Overdue Medications</p>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3}>
          <Card className="text-center">
            <Card.Body>
              <h4 className="text-success">
                {patients.filter(p => p.status === 'stable').length}
              </h4>
              <p className="mb-0">Stable Patients</p>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row>
        {/* Patient List */}
        <Col lg={8}>
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">My Patients</h5>
            </Card.Header>
            <Card.Body>
              <Table responsive>
                <thead>
                  <tr>
                    <th>Patient</th>
                    <th>Room</th>
                    <th>Condition</th>
                    <th>Status</th>
                    <th>Priority</th>
                    <th>Last Vitals</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {patients.map(patient => (
                    <tr key={patient.id}>
                      <td>
                        <div>
                          <strong>{patient.name}</strong>
                          <br />
                          <small className="text-muted">Age: {patient.age}</small>
                          <br />
                          <small className="text-muted">Dr: {patient.doctor}</small>
                        </div>
                      </td>
                      <td>
                        <Badge bg="light" text="dark">{patient.room}</Badge>
                      </td>
                      <td>{patient.condition}</td>
                      <td>{getStatusBadge(patient.status)}</td>
                      <td>{getPriorityBadge(patient.priority)}</td>
                      <td>
                        <span
                          className={
                            getVitalsStatus(patient.lastVitalsCheck) === 'overdue'
                              ? 'text-danger'
                              : 'text-success'
                          }
                        >
                          {patient.lastVitalsCheck}
                        </span>
                        {getVitalsStatus(patient.lastVitalsCheck) === 'overdue' && (
                          <>
                            <br />
                            <small className="text-danger">Overdue</small>
                          </>
                        )}
                      </td>
                      <td>
                        <Button
                          variant="primary"
                          size="sm"
                          className="me-1 mb-1"
                          onClick={() => handleRecordVitals(patient)}
                        >
                          Vitals
                        </Button>
                        <Button
                          variant="outline-primary"
                          size="sm"
                          className="me-1 mb-1"
                          onClick={() => handleNursingNotes(patient)}
                        >
                          Notes
                        </Button>
                        <Button
                          variant="secondary"
                          size="sm"
                          className="mb-1"
                          onClick={() => openRecordFor(patient)}
                        >
                          View Record
                        </Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Col>

        {/* Medication Schedule & Quick Actions */}
        <Col lg={4}>
          {/* Quick Actions */}
          <Card className="mb-4">
            <Card.Header>
              <h5 className="mb-0">Quick Actions</h5>
            </Card.Header>
            <Card.Body>
              <div className="d-grid gap-2">
                <Button
                  variant="primary"
                  onMouseDown={(e) => e.preventDefault()}
                  onClick={(e) => {
                    e.currentTarget.blur();
                    // your emergency handler
                  }}
                >
                  Emergency Call
                </Button>

                <Button
                  variant="primary"
                  onMouseDown={(e) => e.preventDefault()}
                  onClick={(e) => {
                    e.currentTarget.blur();
                    // your page doctor handler
                  }}
                >
                  Page Doctor
                </Button>

                <Button
                  variant="primary"
                  onMouseDown={(e) => e.preventDefault()}
                  onClick={(e) => {
                    e.currentTarget.blur();
                    setShowAssignmentModal(true);
                  }}
                >
                  Manage Patients
                </Button>

                <Button
                  variant="outline-primary"
                  onMouseDown={(e) => e.preventDefault()}
                  onClick={(e) => {
                    e.currentTarget.blur();
                    setShowPatientSearch(true);
                  }}
                >
                  Search Records
                </Button>

                <Button
                  variant="outline-primary"
                  onMouseDown={(e) => e.preventDefault()}
                  onClick={(e) => {
                    e.currentTarget.blur();
                    openShiftReport();
                  }}
                >
                  Shift Report
                </Button>
              </div>
            </Card.Body>
          </Card>

          {/* Medication Schedule */}
          <Card>
            <Card.Header>
              <h5 className="mb-0">Medication Schedule</h5>
            </Card.Header>
            <Card.Body style={{ maxHeight: '400px', overflowY: 'auto' }}>
              {medications.map(medication => (
                <div key={medication.id} className="border-bottom pb-2 mb-2">
                  <div className="d-flex justify-content-between align-items-start">
                    <div>
                      <strong>{medication.patientName}</strong>
                      <br />
                      <span className="text-primary">{medication.medication}</span>
                      <br />
                      <small className="text-muted">
                        {medication.time} | {medication.route}
                      </small>
                      <br />
                      <small className="text-muted">{medication.instructions}</small>
                      <br />
                      {getMedicationStatusBadge(medication.status)}
                    </div>
                    <Button
                      variant={
                        medication.status === 'administered'
                          ? 'primary'
                          : 'outline-primary'
                      }
                      size="sm"
                      onClick={() => handleMedicationAdmin(medication)}
                      disabled={medication.status === 'administered'}
                    >
                      {medication.status === 'administered' ? 'Done' : 'Admin'}
                    </Button>
                  </div>
                </div>
              ))}
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Vitals Recording Modal */}
      <Modal show={showVitalsModal} onHide={() => setShowVitalsModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Record Vitals - {selectedPatient?.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Row>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Blood Pressure (mmHg)</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="120/80"
                    value={newVitals.bloodPressure}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, bloodPressure: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Heart Rate (bpm)</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="72"
                    value={newVitals.heartRate}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, heartRate: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Temperature (F)</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.1"
                    placeholder="98.6"
                    value={newVitals.temperature}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, temperature: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={6}>
                <Form.Group className="mb-3">
                  <Form.Label>Oxygen Saturation (%)</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="98"
                    value={newVitals.oxygenSaturation}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, oxygenSaturation: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={4}>
                <Form.Group className="mb-3">
                  <Form.Label>Respiratory Rate</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="16"
                    value={newVitals.respiratoryRate}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, respiratoryRate: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={4}>
                <Form.Group className="mb-3">
                  <Form.Label>Weight (lbs)</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="150"
                    value={newVitals.weight}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, weight: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
              <Col md={4}>
                <Form.Group className="mb-3">
                  <Form.Label>Height (in)</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="68"
                    value={newVitals.height}
                    onChange={(e) =>
                      setNewVitals({ ...newVitals, height: e.target.value })
                    }
                  />
                </Form.Group>
              </Col>
            </Row>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowVitalsModal(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={saveVitals}>
            Save Vitals
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Medication Administration Modal */}
      <Modal show={showMedicationModal} onHide={() => setShowMedicationModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Administer Medication</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {selectedPatient && (
            <div>
              <p><strong>Patient:</strong> {selectedPatient.patientName}</p>
              <p><strong>Medication:</strong> {selectedPatient.medication}</p>
              <p><strong>Route:</strong> {selectedPatient.route}</p>
              <p><strong>Time:</strong> {selectedPatient.time}</p>
              <p><strong>Instructions:</strong> {selectedPatient.instructions}</p>
              <Alert variant="warning">
                Please confirm that you are administering the correct medication to the correct patient.
              </Alert>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowMedicationModal(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={administerMedication}>
            Confirm Administration
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Nursing Notes Modal */}
      <Modal show={showNotesModal} onHide={() => setShowNotesModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Nursing Notes - {selectedPatient?.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Control
                as="textarea"
                rows={6}
                value={nursingNotes}
                onChange={(e) => setNursingNotes(e.target.value)}
                placeholder="Enter observations, patient interactions, care provided..."
              />
            </Form.Group>
            <Alert variant="info">
              <small>These notes will be added to the patient's medical record and shared with the care team.</small>
            </Alert>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowNotesModal(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={saveNursingNotes}>
            Save Notes
          </Button>
        </Modal.Footer>
      </Modal>

      {/* SHIFT REPORT MODAL */}
      <Modal show={showShiftModal} onHide={() => setShowShiftModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Shift Report</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Row className="mb-3">
            <Col md={6}>
              <Form.Label>Shift Start</Form.Label>
              <Form.Control
                type="datetime-local"
                value={shiftStart}
                onChange={e => setShiftStart(e.target.value)}
              />
            </Col>
            <Col md={6}>
              <Form.Label>Shift End</Form.Label>
              <Form.Control
                type="datetime-local"
                value={shiftEnd}
                onChange={e => setShiftEnd(e.target.value)}
              />
            </Col>
          </Row>

          <div className="d-flex gap-2 mb-2">
            <Button variant="outline-primary" size="sm" onClick={regenerateReport}>
              Regenerate
            </Button>
            <Button variant="secondary" size="sm" onClick={copyReportToClipboard}>
              Copy
            </Button>
            <Button variant="primary" size="sm" onClick={downloadReport}>
              Download (.txt)
            </Button>
            <Button variant="secondary" size="sm" onClick={printReport}>
              Print
            </Button>
          </div>

          <Form.Control as="textarea" rows={14} readOnly value={shiftReport} />
          <Alert variant="light" className="mt-2">
            <small>Tip: Use <strong>Regenerate</strong> after updating vitals, meds, or notes.</small>
          </Alert>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowShiftModal(false)}>Close</Button>
        </Modal.Footer>
      </Modal>

      {/* Patient Details Modal */}
      <Modal
        show={showPatientDetailsModal}
        onHide={() => setShowPatientDetailsModal(false)}
        size="lg"
      >
        <Modal.Header closeButton>
          <Modal.Title>
            Patient Details  {selectedPatient?.name || selectedPatient?.patientName}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {!selectedPatient ? (
            <p className="text-muted m-0">No patient selected.</p>
          ) : (
            <>
              <Row className="mb-3">
                <Col md={6}>
                  <div><strong>Age:</strong> {selectedPatient.age ?? ''}</div>
                  <div><strong>Sex:</strong> {selectedPatient.sex ?? ''}</div>
                  <div><strong>Room:</strong> {selectedPatient.room ?? ''}</div>
                  <div><strong>Doctor:</strong> {selectedPatient.doctor ?? ''}</div>
                  <div>
                    <strong>Priority:</strong>{' '}
                    {selectedPatient.priority ? (
                      <Badge bg={selectedPatient.priority === 'urgent' ? 'danger' : selectedPatient.priority === 'high' ? 'warning' : 'secondary'}>
                        {String(selectedPatient.priority).toUpperCase()}
                      </Badge>
                    ) : ''}
                  </div>
                </Col>
                <Col md={6}>
                  <div><strong>Phone:</strong> {selectedPatient.phone ?? ''}</div>
                  <div><strong>Email:</strong> {selectedPatient.email ?? ''}</div>
                  <div><strong>Address:</strong> {selectedPatient.address ?? ''}</div>
                  <div>
                    <strong>Allergies:</strong>{' '}
                    {Array.isArray(selectedPatient.allergies) && selectedPatient.allergies.length
                      ? selectedPatient.allergies.join(', ')
                      : 'None'}
                  </div>
                </Col>
              </Row>

              {(selectedPatient.condition || selectedPatient.chiefComplaint) && (
                <Card className="mb-3">
                  <Card.Body>
                    {selectedPatient.condition && (
                      <div className="mb-1">
                        <strong>Condition:</strong> {selectedPatient.condition}
                      </div>
                    )}
                    {selectedPatient.chiefComplaint && (
                      <div className="mb-1">
                        <strong>Chief Complaint:</strong> {selectedPatient.chiefComplaint}
                      </div>
                    )}
                    {(selectedPatient.department || selectedPatient.triageLevel) && (
                      <div className="mt-1">
                        {selectedPatient.department && (
                          <Badge bg="info" className="me-2">
                            {String(selectedPatient.department).toUpperCase()}
                          </Badge>
                        )}
                        {selectedPatient.triageLevel && (
                          <Badge bg={
                            selectedPatient.triageLevel <= 1 ? 'danger'
                            : selectedPatient.triageLevel === 2 ? 'warning'
                            : selectedPatient.triageLevel === 3 ? 'info'
                            : 'secondary'
                          }>
                            TRIAGE {selectedPatient.triageLevel}
                          </Badge>
                        )}
                      </div>
                    )}
                  </Card.Body>
                </Card>
              )}

              {(selectedPatient.vitalSigns || selectedPatient.lastVitalsCheck) && (
                <Card>
                  <Card.Header><strong>Recent Vitals</strong></Card.Header>
                  <Card.Body>
                    {selectedPatient.vitalSigns ? (
                      <Row>
                        <Col md={4}><div><small>BP</small><div>{selectedPatient.vitalSigns.bloodPressure ?? ''}</div></div></Col>
                        <Col md={4}><div><small>HR</small><div>{selectedPatient.vitalSigns.heartRate ?? ''}</div></div></Col>
                        <Col md={4}><div><small>Temp</small><div>{selectedPatient.vitalSigns.temperature ?? ''}</div></div></Col>
                        <Col md={4}><div className="mt-2"><small>RR</small><div>{selectedPatient.vitalSigns.respiratoryRate ?? ''}</div></div></Col>
                        <Col md={4}><div className="mt-2"><small>SpO</small><div>{selectedPatient.vitalSigns.oxygenSaturation ?? ''}{selectedPatient.vitalSigns.oxygenSaturation ? '%' : ''}</div></div></Col>
                        <Col md={4}><div className="mt-2"><small>Pain</small><div>{selectedPatient.vitalSigns.painLevel ?? ''}</div></div></Col>
                      </Row>
                    ) : (
                      <div>Last vitals check: {selectedPatient.lastVitalsCheck ?? ''}</div>
                    )}
                  </Card.Body>
                </Card>
              )}
            </>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="outline-primary"
            onClick={() => {
              handleViewPatientRecords(selectedPatient);
              setShowPatientDetailsModal(false);
            }}
          >
            Open Record
          </Button>
          <Button variant="secondary" onClick={() => setShowPatientDetailsModal(false)}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Patient Search Modal */}
      <PatientSearchModal
        show={showPatientSearch}
        onHide={() => setShowPatientSearch(false)}
        patients={patients}
        onOpenRecord={(p) => {
          setShowPatientSearch(false);
          openRecordFor(p);
        }}
      />

      {/* Medical Record Module */}
      {patientForRecord && (
        <MedicalRecordModule
          key={patientForRecord.id}
          show={showMedicalModal}
          onHide={() => { setShowMedicalModal(false); setMedicalRecordPatient(null); setSelectedPatient(null); }}
          currentUser={{ id: 'N0001', role: 'nurse', name: 'Nurse Johnson' }}
          patient={patientForRecord}
        />
      )}

      <PatientAssignmentModule
        show={showAssignmentModal}
        onHide={() => setShowAssignmentModal(false)}
        currentUser={{ id: 'N001', role: 'nurse', name: 'Nurse Johnson' }}
        onAssignmentUpdate={handleAssignmentUpdate}
        onViewDetails={(p) => handleViewPatientDetails(p)}
        onViewRecord={(p) => handleViewPatientRecords(p)}
        enableRecordQuickOpen={true}
      />
    </Container>
  );
};

export default NurseDashboard;