// src/components/MedicalRecordModule.js
import React, { useEffect, useState } from 'react';
import {
  Modal, Button, Row, Col, Card, Table, Badge,
  Form, InputGroup, Alert
} from 'react-bootstrap';

/** -----------------------------------------------------------
 * Permission system
 * ----------------------------------------------------------- */
const DEFAULT_PERMISSION_MATRIX = {
  patient: {
    profile: ['view'],
    allergies: ['view'],
    meds: ['view'],
    problems: ['view'],
    encounters: ['view'],
    labs: ['view'],
    imaging: ['view'],
    immunizations: ['view'],
    documents: ['view', 'download'],
  },
  nurse: {
    profile: ['view', 'edit'],
    allergies: ['view', 'create', 'edit'],
    meds: ['view'],
    problems: ['view'],
    encounters: ['view', 'create'],
    labs: ['view'],
    imaging: ['view'],
    immunizations: ['view', 'create', 'edit'],
    documents: ['view', 'upload', 'download'],
  },
  doctor: {
    profile: ['view', 'edit'],
    allergies: ['view', 'create', 'edit', 'delete'],
    meds: ['view', 'create', 'edit'],
    problems: ['view', 'create', 'edit'],
    encounters: ['view', 'create', 'edit'],
    labs: ['view', 'order', 'view-results'],
    imaging: ['view', 'order', 'view-results'],
    immunizations: ['view', 'create', 'edit'],
    documents: ['view', 'upload', 'download'],
  },
  admin: {
    profile: ['view', 'edit'],
    allergies: ['view', 'create', 'edit', 'delete'],
    meds: ['view', 'create', 'edit', 'delete'],
    problems: ['view', 'create', 'edit', 'delete'],
    encounters: ['view', 'create', 'edit', 'delete'],
    labs: ['view', 'order', 'view-results', 'delete'],
    imaging: ['view', 'order', 'view-results', 'delete'],
    immunizations: ['view', 'create', 'edit', 'delete'],
    documents: ['view', 'upload', 'download', 'delete'],
  },
};

const hasPerm = (role, feature, action, matrix) => {
  const m = matrix || DEFAULT_PERMISSION_MATRIX;
  const allow = m?.[role]?.[feature] || [];
  return allow.includes(action);
};

/** Small UI helpers */
const Status = ({ s }) => {
  const map = { active: 'success', completed: 'secondary', pending: 'warning' };
  return <Badge bg={map[s] || 'secondary'}>{s}</Badge>;
};

/** A labeled field that auto-switches between plaintext and input */
const Field = ({
  label,
  value,
  onChange,
  type = 'text',
  as = 'input',
  readOnly = true,
  options,          // for selects: [{value,label}]
  placeholder,
}) => (
  <Form.Group as={Row} className="mb-3">
    <Form.Label column sm={4} className="fw-semibold text-dark">
      {label}
    </Form.Label>
    <Col sm={8}>
      {readOnly ? (
        <div className="form-control-plaintext text-dark">{value || '-'}</div>
      ) : as === 'select' ? (
        <Form.Select value={value} onChange={onChange}>
          {options?.map(opt => (
            <option key={opt.value} value={opt.value}>{opt.label}</option>
          ))}
        </Form.Select>
      ) : (
        <Form.Control
          type={type}
          value={value ?? ''}
          onChange={onChange}
          placeholder={placeholder}
        />
      )}
    </Col>
  </Form.Group>
);

/** -----------------------------------------------------------
 * Component
 * ----------------------------------------------------------- */
const MedicalRecordModule = ({
  show,
  onHide,
  currentUser = { id: 'P001234', role: 'patient', name: 'Sarah Johnson' },
  patient = {
    id: 'P001234',
    name: 'Sarah Johnson',
    dob: '1990-05-15',
    sex: 'F',
    phone: '+233-24-555-0123',
    email: 'sarah.johnson@email.com',
    address: '123 Main St, Accra, Ghana',
    bloodType: 'O+',
    allergies: ['Penicillin', 'Shellfish'],
  },
  permissionMatrix
}) => {
  const role = currentUser.role || 'patient';
  const can = (feature, action) => hasPerm(role, feature, action, permissionMatrix);

  // Add active tab state
  const [activeTab, setActiveTab] = useState('profile');

  // Mock record state (swap with API calls later)
  const [loading, setLoading] = useState(false);
  const [profile, setProfile] = useState({
    name: patient.name,
    dob: patient.dob,
    sex: patient.sex || 'F',
    phone: patient.phone,
    email: patient.email,
    address: patient.address,
    bloodType: patient.bloodType
  });
  const [allergies, setAllergies] = useState(patient.allergies || []);
  const [meds, setMeds] = useState([
    { id: 'rx1', name: 'Lisinopril 10mg', sig: '1 tab daily', status: 'active' },
    { id: 'rx2', name: 'Metformin 500mg', sig: '1 tab BID with meals', status: 'active' },
  ]);
  const [problems, setProblems] = useState([
    { id: 'p1', term: 'Hypertension', status: 'active' },
    { id: 'p2', term: 'Prediabetes', status: 'active' },
  ]);
  const [encounters, setEncounters] = useState([
    { id: 'e1', date: '2025-09-08', type: 'Office visit', provider: 'Dr. Smith', notes: 'BP check' },
  ]);
  const [labs, setLabs] = useState([
    { id: 'l1', name: 'CBC', ordered: '2025-09-05', status: 'completed', resultSummary: 'Normal' },
    { id: 'l2', name: 'Lipid Panel', ordered: '2025-08-28', status: 'completed', resultSummary: 'High cholesterol' },
  ]);
  const [imaging, setImaging] = useState([
    { id: 'img1', name: 'Chest X-Ray', date: '2025-09-10', status: 'pending' },
  ]);
  const [immunizations, setImmunizations] = useState([
    { id: 'imm1', vaccine: 'Influenza', date: '2025-01-05' },
  ]);
  const [documents, setDocuments] = useState([
    { id: 'd1', title: 'Referral Letter.pdf', uploaded: '2025-07-11', size: '120 KB' },
  ]);

  useEffect(() => {
    if (!show) return;
    (async () => {
      setLoading(true);
      // TODO: GET /api/records/:patientId
      await new Promise(r => setTimeout(r, 250));
      setLoading(false);
    })();
  }, [show]);

  /** ------------- UI sections ------------- */
  const ProfileView = () => {
    const editable = can('profile', 'edit');
    return (
      <Card className="bg-white border">
        <Card.Body>
          <Form>
            <Row>
              <Col md={6}>
                <Field
                  label="Full Name"
                  value={profile.name}
                  onChange={e => setProfile({ ...profile, name: e.target.value })}
                  readOnly={!editable}
                />
                <Field
                  label="Date of Birth"
                  type="date"
                  value={profile.dob}
                  onChange={e => setProfile({ ...profile, dob: e.target.value })}
                  readOnly={!editable}
                />
                <Field
                  label="Sex"
                  as="select"
                  value={profile.sex}
                  onChange={e => setProfile({ ...profile, sex: e.target.value })}
                  options={[
                    { value: 'F', label: 'Female' },
                    { value: 'M', label: 'Male' },
                    { value: 'O', label: 'Other' },
                  ]}
                  readOnly={!editable}
                />
                <Field
                  label="Phone"
                  value={profile.phone}
                  onChange={e => setProfile({ ...profile, phone: e.target.value })}
                  readOnly={!editable}
                />
                <Field
                  label="Email"
                  type="email"
                  value={profile.email}
                  onChange={e => setProfile({ ...profile, email: e.target.value })}
                  readOnly={!editable}
                />
                <Field
                  label="Address"
                  value={profile.address}
                  onChange={e => setProfile({ ...profile, address: e.target.value })}
                  readOnly={!editable}
                />
                <Field
                  label="Blood Type"
                  value={profile.bloodType}
                  onChange={e => setProfile({ ...profile, bloodType: e.target.value })}
                  readOnly={!editable}
                />
              </Col>
              <Col md={6}>
                {/* Empty column for spacing or future use */}
              </Col>
            </Row>

            {editable && (
              <div className="mt-2">
                <Button variant="dark">Save Profile</Button>
              </div>
            )}
          </Form>
        </Card.Body>
      </Card>
    );
  };

  const AllergiesView = () => {
    const [newAllergy, setNewAllergy] = useState('');
    const add = () => {
      if (!newAllergy.trim()) return;
      setAllergies([...allergies, newAllergy.trim()]);
      setNewAllergy('');
    };
    const remove = (idx) => setAllergies(allergies.filter((_, i) => i !== idx));

    return (
      <Card className="bg-white border">
        <Card.Body>
          {can('allergies', 'create') && (
            <InputGroup className="mb-3">
              <Form.Control
                placeholder="Add new allergy"
                value={newAllergy}
                onChange={(e) => setNewAllergy(e.target.value)}
              />
              <Button variant="dark" onClick={add}>Add</Button>
            </InputGroup>
          )}

          {allergies.length === 0 ? (
            <Alert variant="light" className="text-dark border">No recorded allergies.</Alert>
          ) : (
            <div>
              {allergies.map((a, i) => (
                <div key={i} className="d-flex justify-content-between align-items-center border rounded p-2 mb-2">
                  <span className="text-dark">{a}</span>
                  {can('allergies', 'delete') && (
                    <Button size="sm" variant="outline-danger" onClick={() => remove(i)}>
                      Remove
                    </Button>
                  )}
                </div>
              ))}
            </div>
          )}
        </Card.Body>
      </Card>
    );
  };

  const MedsView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Medication</th>
              <th>Sig</th>
              <th>Status</th>
              {can('meds', 'edit') && <th style={{width: 120}}>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {meds.map(rx => (
              <tr key={rx.id}>
                <td className="fw-semibold">{rx.name}</td>
                <td>{rx.sig}</td>
                <td><Status s={rx.status} /></td>
                {can('meds', 'edit') && (
                  <td>
                    <Button size="sm" variant="outline-dark" className="me-2">Edit</Button>
                    {can('meds', 'delete') && <Button size="sm" variant="outline-danger">Delete</Button>}
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </Table>
        {can('meds', 'create') && <Button className="mt-3" variant="dark">Add Medication</Button>}
      </Card.Body>
    </Card>
  );

  const ProblemsView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Problem</th>
              <th>Status</th>
              {can('problems', 'edit') && <th style={{width: 120}}>Actions</th>}
            </tr>
          </thead>
        <tbody>
            {problems.map(p => (
              <tr key={p.id}>
                <td>{p.term}</td>
                <td><Status s={p.status} /></td>
                {can('problems', 'edit') && (
                  <td>
                    <Button size="sm" variant="outline-dark" className="me-2">Edit</Button>
                    {can('problems', 'delete') && <Button size="sm" variant="outline-danger">Delete</Button>}
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </Table>
        {can('problems', 'create') && <Button className="mt-3" variant="dark">Add Problem</Button>}
      </Card.Body>
    </Card>
  );

  const EncountersView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Date</th>
              <th>Type</th>
              <th>Provider</th>
              <th>Notes</th>
              {can('encounters', 'edit') && <th style={{width: 120}}>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {encounters.map(e => (
              <tr key={e.id}>
                <td>{e.date}</td>
                <td>{e.type}</td>
                <td>{e.provider}</td>
                <td>{e.notes}</td>
                {can('encounters', 'edit') && (
                  <td>
                    <Button size="sm" variant="outline-dark" className="me-2">Edit</Button>
                    {can('encounters', 'delete') && <Button size="sm" variant="outline-danger">Delete</Button>}
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </Table>
        {can('encounters', 'create') && <Button className="mt-3" variant="dark">New Encounter</Button>}
      </Card.Body>
    </Card>
  );

  const LabsView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Test</th>
              <th>Ordered</th>
              <th>Status</th>
              <th>Summary</th>
              {can('labs', 'order') && <th style={{width: 120}}>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {labs.map(l => (
              <tr key={l.id}>
                <td>{l.name}</td>
                <td>{l.ordered}</td>
                <td><Status s={l.status} /></td>
                <td>{l.resultSummary}</td>
                {can('labs', 'order') && (
                  <td>
                    <Button size="sm" variant="outline-dark" className="me-2">View</Button>
                    {can('labs', 'delete') && <Button size="sm" variant="outline-danger">Delete</Button>}
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </Table>
        {can('labs', 'order') && <Button className="mt-3" variant="dark">Order Lab</Button>}
      </Card.Body>
    </Card>
  );

  const ImagingView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Study</th>
              <th>Date</th>
              <th>Status</th>
              {can('imaging', 'order') && <th style={{width: 120}}>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {imaging.map(i => (
              <tr key={i.id}>
                <td>{i.name}</td>
                <td>{i.date}</td>
                <td><Status s={i.status} /></td>
                {can('imaging', 'order') && (
                  <td>
                    <Button size="sm" variant="outline-dark" className="me-2">View</Button>
                    {can('imaging', 'delete') && <Button size="sm" variant="outline-danger">Delete</Button>}
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </Table>
        {can('imaging', 'order') && <Button className="mt-3" variant="dark">Order Imaging</Button>}
      </Card.Body>
    </Card>
  );

  const ImmunizationsView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Vaccine</th>
              <th>Date</th>
              {can('immunizations', 'edit') && <th style={{width: 120}}>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {immunizations.map(im => (
              <tr key={im.id}>
                <td>{im.vaccine}</td>
                <td>{im.date}</td>
                {can('immunizations', 'edit') && (
                  <td>
                    <Button size="sm" variant="outline-dark" className="me-2">Edit</Button>
                    {can('immunizations', 'delete') && <Button size="sm" variant="outline-danger">Delete</Button>}
                  </td>
                )}
              </tr>
            ))}
          </tbody>
        </Table>
        {can('immunizations', 'create') && <Button className="mt-3" variant="dark">Add Immunization</Button>}
      </Card.Body>
    </Card>
  );

  const DocumentsView = () => (
    <Card className="bg-white border">
      <Card.Body>
        <Table responsive bordered striped hover size="sm" className="mb-0">
          <thead className="table-light">
            <tr>
              <th>Title</th>
              <th>Uploaded</th>
              <th>Size</th>
              <th style={{width: 220}}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {documents.map(d => (
              <tr key={d.id}>
                <td className="fw-semibold">{d.title}</td>
                <td>{d.uploaded}</td>
                <td>{d.size}</td>
                <td>
                  <Button
                    size="sm"
                    variant="outline-dark"
                    className="me-2"
                    disabled={!can('documents', 'download')}
                  >
                    Download
                  </Button>
                  {can('documents', 'upload') && (
                    <Button size="sm" variant="outline-success" className="me-2">Replace</Button>
                  )}
                  {can('documents', 'delete') && (
                    <Button size="sm" variant="outline-danger">Delete</Button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
        {can('documents', 'upload') && <Button className="mt-3" variant="dark">Upload Document</Button>}
      </Card.Body>
    </Card>
  );

  // Tab configuration
  const tabs = [
    { key: 'profile', title: 'Profile', component: ProfileView },
    { key: 'allergies', title: 'Allergies', component: AllergiesView },
    { key: 'meds', title: 'Medications', component: MedsView },
    { key: 'problems', title: 'Problems', component: ProblemsView },
    { key: 'encounters', title: 'Encounters', component: EncountersView },
    { key: 'labs', title: 'Labs', component: LabsView },
    { key: 'imaging', title: 'Imaging', component: ImagingView },
    { key: 'immunizations', title: 'Immunizations', component: ImmunizationsView },
    { key: 'documents', title: 'Documents', component: DocumentsView },
  ];

  return (
    <Modal show={show} onHide={onHide} size="xl">
      <Modal.Header>
        <Modal.Title className="text-dark d-flex justify-content-between align-items-center w-100">
          <span>
            Medical Record  {patient.name}{' '}
            <span className="text-muted ms-2">({patient.id})</span>
          </span>
          <button 
            type="button" 
            className="btn btn-sm"
            onClick={onHide}
            style={{
              background: 'var(--warm-black)',
              color: 'white',
              border: '1px solid var(--warm-black)',
              borderRadius: '6px',
              padding: '8px 12px',
              fontSize: '14px',
              cursor: 'pointer'
            }}
          >
            Close
          </button>
        </Modal.Title>
      </Modal.Header>

      <Modal.Body style={{ maxHeight: '75vh', overflowY: 'auto' }}>
        {loading ? (
          <Alert variant="light" className="border text-dark">Loading record</Alert>
        ) : (
          <>
            {/* Custom Tab Navigation */}
            <div className="medical-record-tabs">
              {tabs.map(tab => (
                <button
                  key={tab.key}
                  className={`medical-tab ${activeTab === tab.key ? 'active' : ''}`}
                  onClick={() => setActiveTab(tab.key)}
                >
                  {tab.title}
                </button>
              ))}
            </div>

            {/* Tab Content */}
            <div className="medical-tab-content">
              {(() => {
                const activeTabConfig = tabs.find(tab => tab.key === activeTab);
                const Component = activeTabConfig?.component;
                return Component ? <Component /> : null;
              })()}
            </div>
          </>
        )}
      </Modal.Body>

      <Modal.Footer>
        <small className="text-muted me-auto">
          Viewing as <strong className="text-dark">{role}</strong>
        </small>
      </Modal.Footer>
    </Modal>
  );
};

export default MedicalRecordModule;